import { Box, Button, chakra, Flex, Image, Spinner } from "@chakra-ui/react";
import React from "react";
import { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { ModalContext } from "../../system/context/ModalContext";
import { usePrevious } from "../../system/hooks/usePrevious";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_HEADER_TEXT_COLOR,
  APP_SECONDARY_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";
import { SuccessCollectionProps } from "./types";

function SuccessCollection() {
  let navigate = useNavigate();
  let location = useLocation();
  const props: SuccessCollectionProps = location.state;
  const { uiText } = useUiText();
  const { walletModal } = React.useContext(ModalContext);
  const [pageLoading, setPageLoading] = useState(true);
  const prevId = usePrevious(props?.id);

  useEffect(() => {
    if (props) {
      setPageLoading(false);
      if (props.id !== prevId) {
        walletModal.onOpen(1, props.id);
      }
    } else {
      navigate("/home", { replace: true });
    }
  }, [props, navigate, walletModal, prevId]);

  return pageLoading ? (
    <Spinner color={APP_SECONDARY_COLOR} />
  ) : (
    <Box
      display={"flex"}
      justifyContent={"flex-start"}
      px={"150px"}
      w="100%"
      height="89vh"
      alignSelf={"stretch"}
    >
      <Flex justifyContent={"center"} flexDir={"column"}>
        <Flex flexDir={"row"} mb="55px">
          <Image
            src="./celebration.png"
            width="240px"
            height="240px"
            objectFit={"contain"}
          />
          <Box>
            <chakra.h1
              fontSize={"36px"}
              textAlign={"left"}
              fontWeight={"semibold"}
              color={HOMEPAGE_BACKGROUND_COLOR}
            >
              {uiText.successCollectionTitle}
            </chakra.h1>
            <chakra.h1
              width="342px"
              fontSize={"36px"}
              textAlign={"left"}
              fontWeight={"medium"}
              color={HOMEPAGE_BACKGROUND_COLOR}
            >
              {uiText.successCollectionSubTitle1} <br />
              {uiText.successCollectionSubTitle2}
            </chakra.h1>
          </Box>
        </Flex>
        <chakra.h1 fontSize={"16px"} textAlign={"left"}>
          {uiText.successCollectionSubTitle3}
        </chakra.h1>
        <Flex flexDir={"row"} mt="66px">
          <Button
            width={"182px"}
            mr="12px"
            fontSize={"12px"}
            onClick={() => navigate("/kitchen", { replace: true })}
            bgColor={HOMEPAGE_BACKGROUND_COLOR}
            color={APP_HEADER_TEXT_COLOR}
          >
            {uiText.successCollectionButton1}
          </Button>
          <Button
            bgColor={APP_HEADER_TEXT_COLOR}
            width={"182px"}
            fontSize={"12px"}
            borderWidth={"1px"}
            onClick={() => navigate("/create-collection", { replace: true })}
            borderColor={HOMEPAGE_BACKGROUND_COLOR}
          >
            {uiText.successCollectionButton2}
          </Button>
        </Flex>
      </Flex>
      <Image
        position={"absolute"}
        right={700}
        bottom={150}
        src="./arrow-point-right.png"
        width="117px"
        height="62px"
        objectFit={"contain"}
      />
    </Box>
  );
}

export default SuccessCollection;
