import { Box, Button, chakra, Image } from "@chakra-ui/react";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_HEADER_TEXT_COLOR,
  FILE_ERROR_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";
import { FileErrorModalProps } from "./types";

function FileErrorModal({ onClose, filenames }: FileErrorModalProps) {
  const { uiText } = useUiText();

  return (
    <Box
      top={0}
      bottom={0}
      left={0}
      right={0}
      position={"fixed"}
      bg="rgba(0,0,0,0.5)"
      zIndex={10}
    >
      <Box
        bgColor={APP_HEADER_TEXT_COLOR}
        position={"absolute"}
        top={"50%"}
        left={"50%"}
        transform={"translate(-50%, -50%)"}
        width={"360px"}
        height={"360px"}
        zIndex={20}
        borderRadius={"8px"}
        display={"flex"}
        flexDir={"column"}
        alignItems={"center"}
      >
        <Button
          onClick={onClose}
          position={"absolute"}
          mt={"5px"}
          mr={"5px"}
          alignSelf={"flex-end"}
          bgColor={"transparent"}
        >
          <Image
            src="./Close.png"
            width="14px"
            height="14px"
            objectFit={"contain"}
          />
        </Button>
        <Image
          src="./error.png"
          width="52px"
          height="52px"
          objectFit={"contain"}
          mt="52px"
        />
        <chakra.h2
          fontSize={"18px"}
          fontWeight={"semibold"}
          color={HOMEPAGE_BACKGROUND_COLOR}
          mt={"16px"}
        >
          {uiText.fileErrorTitle}
        </chakra.h2>
        <chakra.h2
          fontSize={"10px"}
          fontWeight={"medium"}
          color={HOMEPAGE_BACKGROUND_COLOR}
          mt={"6px"}
          mb={"20px"}
          w="250px"
          textAlign={"center"}
        >
          {uiText.fileErrorDescription}
        </chakra.h2>
        {filenames.slice(0, 5).map((name, index) => {
          const splitString = name.split(".");
          const stringLength = splitString.length;
          const ext = splitString[stringLength - 1];
          const filename = name.split(ext)[0];
          return (
            <Box
              key={index}
              display={"flex"}
              w={"150px"}
              alignItems={"start"}
            >
              <chakra.h2
                fontSize={"12px"}
                fontWeight={"medium"}
                color={FILE_ERROR_COLOR}
                mb={"2px"}
                overflow={"hidden"}
                whiteSpace={"nowrap"}
                textOverflow={"ellipsis"}
                textAlign={"left"}
              >
                {filename}
              </chakra.h2>
              <chakra.h2
                fontSize={"12px"}
                fontWeight={"medium"}
                color={FILE_ERROR_COLOR}
              >
                {ext}
              </chakra.h2>
            </Box>
          );
        })}
        {filenames.length > 5 && (
          <chakra.h2
            fontSize={"12px"}
            fontWeight={"medium"}
            textAlign={"left"}
            color={FILE_ERROR_COLOR}
            mb={"2px"}
            w="150px"
            overflow={"hidden"}
            whiteSpace={"nowrap"}
            textOverflow={"ellipsis"}
          >
            {`+ ${filenames.length - 5} more`}
          </chakra.h2>
        )}
      </Box>
    </Box>
  );
}

export default FileErrorModal;
