import {
  Box,
  Flex,
  HStack,
  Text,
  Tooltip,
  Image,
  Button,
  Spinner,
} from "@chakra-ui/react";
import { utils, ContractFactory } from "ethers";
import { useContext } from "react";
import { PrimaryButton } from "../../../components/buttons";
import { WalletContext } from "../../../system/context/WalletContext";
import React from "react";
import { ModalContext } from "../../../system/context/ModalContext";

import { useUiText } from "../../../system/hooks/useUiText";
import {
  APP_ERROR_COLOR,
  APP_IMAGE_PLACEHOLDER_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  APP_WARNING_COLOR,
} from "../../../theme/colors";
import { scrollBarStyles } from "../../../theme/theme";
import { MyCollectionsSectionProps } from "./types";
import { abi, bytecode } from "../../../contracts/MasterchefMasatoshi.json";
import Axios from "axios";
import { Collection, DeployStatusType } from "../../../system/api/types";
import { Link, useNavigate } from "react-router-dom";

export const MyCollectionsSection = ({
  collections,
  setCollection,
  hasReceipt,
  updateCollections,
}: MyCollectionsSectionProps) => {
  const navigate = useNavigate();
  const { uiText } = useUiText();

  const { account, ethereum } = useContext(WalletContext);
  const https = require("https");
  const httpsAgent = new https.Agent({ rejectUnauthorized: false });

  const changeDeployStatus = (index: number, status: DeployStatusType) => {
    const newCollections = [...collections];
    newCollections[index].deployStatus = status;
    updateCollections(newCollections);
  };

  const deployContract = async (
    { name, cost, symbol, total }: Collection,
    index: number
  ) => {
    if (!ethereum) {
      changeDeployStatus(index, "NOTDEPLOYED");
    }
    const signer = ethereum?.getSigner(account);
    const factory = new ContractFactory(abi, bytecode, signer);
    const contract = await factory.deploy(
      name,
      symbol,
      total,
      utils.parseEther(cost.toString()),
      "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2",
      "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2"
    );
    await contract.deployTransaction.wait(3);
    const deployTxHash = contract.deployTransaction.hash;
    const contractAddress = contract.address;
    return {
      deployTxHash,
      contractAddress,
    };
  };

  const deployAndVerify = async (collection: Collection, index: number) => {
    changeDeployStatus(index, "PROCESSING");
    const { deployTxHash, contractAddress } = await deployContract(
      collection,
      index
    ).catch((err) => {
      changeDeployStatus(index, "NOTDEPLOYED");
      return Promise.reject(err);
    });
    await Axios({
      method: "POST",
      responseType: "json",
      httpsAgent,
      url: `https://${process.env.REACT_APP_SERVER_URL}.com/verify`,
      data: {
        collectionId: collection.id,
        deployTxHash,
        contractAddress,
        network: ethereum?.network?.name!,
      },
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => {
        changeDeployStatus(index, "DEPLOYED");
        navigate("/success-collection", {
          replace: true,
          state: { isFromReview: true, id: collection.id },
        });
        return res;
      })
      .catch((err) => {
        changeDeployStatus(index, "NOTDEPLOYED");
        if (!err.response) {
          // network error
          return Promise.reject("Error: Network Error");
        } else {
          return Promise.reject(err);
        }
      });
  };

  const { noReceiptsModal, walletModal } = React.useContext(ModalContext);
  const renderCreateNewCollection = () => {
    if (hasReceipt) {
      return (
        <Link
          to="/create-collection"
          style={{ textDecoration: "none" }}
          onClick={() => walletModal.onClose()}
        >
          <PrimaryButton
            fontSize={"12px"}
            fontWeight={"bold"}
            fontFamily={"heading"}
            text={uiText.startNewCollection}
          />
        </Link>
      );
    } else {
      return (
        <PrimaryButton
          text={uiText.startNewCollection}
          onClick={() => {
            walletModal.onClose();
            noReceiptsModal.onOpen();
          }}
        />
      );
    }
  };
  return (
    <Box
      h={"100%"}
      display={"flex"}
      flexDir={"column"}
      justifyContent={"space-between"}
    >
      <Flex align="center" h="100%" justifyContent={"center"} flexDir="column">
        {!collections.length && (
          <Text color={APP_TEXT_INACTIVE_COLOR}>
            {uiText.noCollectionsMessage}
          </Text>
        )}
        {!!collections.length && (
          <HStack
            overflow="auto"
            maxW="100%"
            sx={scrollBarStyles}
            paddingBottom={"10px"}
          >
            {collections.map((c, index) => (
              <Flex
                flexDir="column"
                onClick={() =>
                  c.deployStatus === "PROCESSING"
                    ? null
                    : c.contractAddress
                    ? setCollection(c.id, collections)
                    : deployAndVerify(c, index)
                }
                key={`collection-${c.id}`}
                _hover={{
                  opacity: !c.contractAddress ? 1 : 0.7,
                  cursor: "pointer",
                }}
              >
                <Box position={"relative"}>
                  <Image
                    h={"236px"}
                    minW={"210px"}
                    objectFit={"cover"}
                    borderRadius={"5px"}
                    bgColor={APP_IMAGE_PLACEHOLDER_COLOR}
                    src={c.collectionImage}
                  />
                  {!c.contractAddress && (
                    <Box
                      top={0}
                      h={"236px"}
                      minW={"210px"}
                      borderRadius={"5px"}
                      bg="rgba(18, 18, 18, 0.5)"
                      display={"flex"}
                      position="absolute"
                      justifyContent={"center"}
                      alignItems={"center"}
                    >
                      <Button
                        bgColor={APP_WARNING_COLOR}
                        w="145px"
                        h="38px"
                        display={"flex"}
                        justifyContent={"center"}
                        alignItems={"center"}
                        borderRadius={"6px"}
                        zIndex={10}
                        disabled={c.deployStatus === "PROCESSING"}
                        _hover={{
                          opacity: 0.9,
                          bgColor: APP_WARNING_COLOR,
                        }}
                        _disabled={{
                          _hover: {
                            opacity: 1,
                            bgColor: APP_WARNING_COLOR,
                          },
                          cursor: "progress",
                        }}
                      >
                        {c.deployStatus === "PROCESSING" ? (
                          <Spinner
                            size="sm"
                            color={APP_PRIMARY_CONTRAST_COLOR}
                          />
                        ) : (
                          <Text
                            fontSize={"12px"}
                            fontWeight={"bold"}
                            color="#FFF"
                          >
                            {uiText.deployCollection}
                          </Text>
                        )}
                      </Button>
                    </Box>
                  )}
                </Box>
                <Text
                  fontFamily={"heading"}
                  mt={"15px"}
                  fontSize="10px"
                  fontWeight={"medium"}
                >
                  {c.symbol}
                </Text>
                <Text
                  fontFamily={"heading"}
                  fontSize="14px"
                  fontWeight="semibold"
                >
                  {c.name}
                </Text>
              </Flex>
            ))}
          </HStack>
        )}
      </Flex>
      <Box placeSelf={"center"}>
        <Tooltip
          label={uiText.getReceiptMessage}
          bg={APP_ERROR_COLOR}
          p="10px 20px 10px 20px"
          placement="top"
          mb={4}
          borderRadius="4px"
          shouldWrapChildren
          hasArrow
          isDisabled={hasReceipt}
        >
          {renderCreateNewCollection()}
        </Tooltip>
      </Box>
    </Box>
  );
};
