import { Box, Flex, Image } from "@chakra-ui/react";
import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { PrimaryButton } from "../../components/buttons";
import CollectorSuccessCard from "../../components/cards/CollectorSuccessCard";
import ErrorCard from "../../components/cards/ErrorCard";
import MintingCard from "../../components/cards/MintingCard";
import WrongNetworkCard from "../../components/cards/WrongNetworkCard";
import { getMintPageContent, getNftImagesByIds } from "../../system/api/api";
import { MintPageContent } from "../../system/api/types";
import { WalletContext } from "../../system/context/WalletContext";
import { useContract } from "../../system/hooks/ethereum/useContract";
import useDeviceDetect from "../../system/hooks/useDeviceDetect";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_PRIMARY_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";

type MintType =
  | "CONTENT"
  | "MINTING"
  | "SUCCESS"
  | "ERROR"
  | "WRONG_NETWORK"
  | "UPDATED";

function Mint() {
  const { isMobile } = useDeviceDetect();
  const [type, setType] = useState<MintType>("CONTENT");
  const isMobileSuccess = isMobile && type === "SUCCESS";
  const { address } = useParams();
  const {
    mintToken,
    saleIsActive,
    transactionPending,
    transactionUnSuccessful,
    transactionSuccessful,
    resetTransactionState,
    contract,
  } = useContract(address);

  const { validation } = useContext(WalletContext);

  const { uiText } = useUiText();
  const [collection, setCollection] = useState<MintPageContent>();

  const handleMint = () => {
    mintToken().catch(() => {
      setType("ERROR");
    });
  };

  const handleCloseNotification = () => {
    setType("CONTENT");
    resetTransactionState();
  };

  const getNftImage = async () => {
    let id;
    await contract
      ?.totalSupply()
      .then((s: number) => {
        id = s - 1;
      })
      .catch((err: any) => {
        console.log(err);
      });

    const isOwner = await contract?.ownerOf(id);
    if (isOwner) {
      getNftImagesByIds(address || "", [id || ""], validation).then((i: any) =>
        console.log(i)
      );
    }
  };

  useEffect(() => {
    getMintPageContent(address || "")
      .then((collection: MintPageContent) => {
        setCollection(collection);
      })
      .catch((err) => {
        console.log("could not find that collection");
      });
  }, [address]);

  useEffect(() => {
    if (transactionPending) {
      setType("MINTING");
    }
    if (transactionUnSuccessful) {
      setType("ERROR");
    }
    if (transactionSuccessful) {
      getNftImage();
      setType("SUCCESS");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [transactionPending, transactionUnSuccessful, transactionSuccessful]);

  const renderContent = () => {
    switch (type) {
      case "ERROR":
        return <ErrorCard onClose={handleCloseNotification} />;
      case "WRONG_NETWORK":
        return <WrongNetworkCard onClose={handleCloseNotification} />;
      case "MINTING":
        return <MintingCard />;
      case "SUCCESS":
        return <CollectorSuccessCard onClose={handleCloseNotification} />;
    }
  };

  return type === "CONTENT" ? (
    <Flex
      bgColor={`rgb(147, 147, 147, 0.1)`}
      w="full"
      h={`${window.innerHeight - 160}px`}
      position={"relative"}
    >
      {collection?.mintPageDesktopImageUrl &&
      collection?.mintPageMobileImageUrl ? (
        <Image
          bg="#F6F6F6"
          src={
            isMobile
              ? collection?.mintPageMobileImageUrl
              : collection?.mintPageDesktopImageUrl
          }
          minW="screen"
          objectFit="contain"
          margin={"auto"}
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
        />
      ) : (
        <Image
          bg="#F6F6F6"
          src={"/image-placeholder.png"}
          w="65px"
          h="65px"
          objectFit="contain"
          margin={"auto"}
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
        />
      )}

      <PrimaryButton
        position={"absolute"}
        bottom={isMobile ? 8 : 20}
        disabled={!saleIsActive}
        left={0}
        right={0}
        margin={"auto"}
        text={uiText.mintNow}
        onClick={() => handleMint()}
        fontWeight="bold"
        bgColor={HOMEPAGE_BACKGROUND_COLOR}
        color={APP_PRIMARY_CONTRAST_COLOR}
        w="238px"
        h="58px"
        _hover={{ opacity: 0.9 }}
      />
    </Flex>
  ) : (
    <>
      <Box
        display={"flex"}
        position={"absolute"}
        top={20}
        bottom={0}
        w="full"
        bgColor={APP_PRIMARY_COLOR}
        justifyContent="center"
        alignItems={isMobileSuccess ? "end" : "center"}
      >
        <Box
          width={isMobileSuccess ? "100vw" : { base: "318px", lg: "352px" }}
          height={isMobileSuccess ? "85vh" : { base: "544px", lg: "600px" }}
          display="flex"
          justifyContent={"center"}
          alignItems="center"
        >
          {renderContent()}
        </Box>
      </Box>
    </>
  );
}

export default Mint;
