import { ChangeEvent, useCallback, useEffect, useState } from "react";
import {
  Flex,
  Input,
  Button,
  Image as ImageChakra,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalCloseButton,
  ModalHeader,
  ModalBody,
  Box,
  Grid,
  ScaleFade,
  SlideFade,
} from "@chakra-ui/react";
import FormDescription from "../forms/FormDescription";
import FormLabel from "../forms/FormLabel";
import { FileWithPath, useDropzone } from "react-dropzone";
import { UploadAttributeModalProps } from "./types";
import AddCollectionButton from "../buttons/AddCollectionButton";
import UploadAttributeCard from "../cards/UploadAttributeCard";
import { DropFileType } from "../../types";
import DeleteAttributeConfirmationModal from "./DeleteAttributeConfirmationModal";
import { useUiText } from "../../system/hooks/useUiText";
import DeleteTraitCard from "../cards/DeleteTraitCard";
import FileErrorModal from "./FileErrorModal";

function UploadAttributeModal({
  isOpen,
  onClose,
  onDeleteAttribute,
  name,
  attributes,
  index,
}: UploadAttributeModalProps) {
  const { uiText } = useUiText();
  const [attributeName, setAttributeName] = useState("");
  const [isDeleteAttributeConfirmation, setIsDeleteAttributeConfirmation] =
    useState(false);
  const [showFileReject, setShowFileReject] = useState(false);
  const [files, setFiles] = useState([] as DropFileType[]);

  const onDrop = useCallback(
    (acceptedFiles: FileWithPath[]) => {
      if (acceptedFiles.length > 0) {
        const name = acceptedFiles[0].path!.split("/")[1];
        if (name) {
          setAttributeName(name);
        }
        const droppedFile = files;
        droppedFile.push(...(acceptedFiles as DropFileType[]));
        const renamed = droppedFile.map((file) => {
          const hashtag = file.name.search("#") !== -1;
          const fileName = file.name.split(hashtag ? "#" : ".")[0];
          const rarity = hashtag ? file.name.split("#")[1].split(".")[0] : "1";
          const newFile = new File([file], `${fileName}#${rarity}.png`, {
            type: file.type,
          });

          return Object.assign(newFile, {
            path: file.path,
            rarityWeight: rarity,
            isDelete: false,
          });
        }) as DropFileType[];

        setFiles(renamed);
      }
    },
    [files]
  );

  useEffect(() => {
    setAttributeName(name || "");
    setFiles(attributes || []);
    return () => {
      setAttributeName("");
    };
  }, [name, attributes, index]);

  const { getRootProps, open, getInputProps, fileRejections } = useDropzone({
    onDrop,
    accept: "image/png",
    maxSize: 10485760,
    noClick: true,
  });

  useEffect(() => {
    if (fileRejections.length) {
      setShowFileReject(true);
    }
  }, [fileRejections]);

  const changeRarityWeight = (
    index: number,
    { currentTarget }: ChangeEvent<HTMLInputElement>
  ) => {
    const array = [...files];
    const renamed = array.map((file, key) => {
      if (index === key) {
        const fileName = file.name.split("#")[0];
        return new File([file], `${fileName}#${currentTarget.value}.png`, {
          type: file.type,
        });
      }
      return file;
    }) as DropFileType[];
    renamed[index].rarityWeight = currentTarget.value;
    setFiles(renamed);
  };

  const changeAttributeName = ({
    currentTarget,
  }: ChangeEvent<HTMLInputElement>) => {
    setAttributeName(currentTarget.value);
  };

  const onCloseModal = () => {
    if (index === undefined) {
      onClose(attributeName, files);
      setFiles([]);
    } else {
      onClose(attributeName, files, index);
    }
  };

  const onDeleteTraits = (index: number) => {
    const array = [...files];
    array.splice(index, 1);
    setFiles(array);
  };

  const onDeleteTraitClick = (index: number, isDelete: boolean) => {
    const array = [...files];
    const previousTrait = array
      .map((el, elIndex) => el.isDelete === true && elIndex !== index)
      .findIndex((el) => el === true);
    if (previousTrait !== -1) {
      array[previousTrait].isDelete = false;
    }
    array[index].isDelete = isDelete;
    setFiles(array);
  };

  const onConfirmDeleteTrait = (isDelete: boolean, index: number) => {
    if (isDelete) {
      onDeleteTraits(index);
    } else {
      onDeleteTraitClick(index, false);
    }
  };

  const onConfirmDeleteAttribute = (isDelete: boolean) => {
    if (isDelete) {
      setAttributeName("");
      setFiles([]);
      onDeleteAttribute(index);
    }
    setIsDeleteAttributeConfirmation(false);
  };

  return (
    <>
      <Modal
        isOpen={isOpen}
        onClose={onCloseModal}
        size={"full"}
        scrollBehavior="inside"
      >
        <ModalOverlay />
        <ModalContent
          backgroundColor={"#121212"}
          display={"flex"}
          h="100%"
          flexWrap={"wrap"}
        >
          <Box
            overflowY={"scroll"}
            backgroundColor={"#FFF"}
            position={"relative"}
            flex={1}
            display={"flex"}
            flexDir={"column"}
            alignSelf={"stretch"}
            mx="58px"
            my="41px"
            px="40px"
            py="25px"
            borderRadius={"8px"}
          >
            {isDeleteAttributeConfirmation && (
              <DeleteAttributeConfirmationModal
                onClose={onConfirmDeleteAttribute}
                name={attributeName}
              />
            )}

            {showFileReject && (
              <FileErrorModal
                onClose={() => setShowFileReject(false)}
                filenames={fileRejections.map((data) => data.file.name)}
              />
            )}

            <ModalHeader>
              <Flex flexDir={"row"}>
                <Box flex={4}>
                  <Input
                    borderWidth={0}
                    focusBorderColor="transparent"
                    id="attributeName"
                    size={"lg"}
                    w="500px"
                    fontSize={"30px"}
                    fontWeight={600}
                    placeholder="Attribute Name"
                    value={attributeName}
                    onChange={changeAttributeName}
                  />
                </Box>
                <Box
                  flex={1}
                  display={"flex"}
                  alignItems={"center"}
                  justifyContent={"center"}
                >
                  <Button
                    onClick={() => setIsDeleteAttributeConfirmation(true)}
                    backgroundColor={"#FFF"}
                    justifyContent={"center"}
                  >
                    <ImageChakra src="/delete.png" h="20px" mr="8px" />
                    {uiText.deleteCollectionModal}
                  </Button>
                </Box>
              </Flex>
            </ModalHeader>
            <ModalCloseButton size={"lg"} />
            <ModalBody {...getRootProps()}>
              <input {...getInputProps()} />

              {files.length !== 0 && (
                <Grid templateColumns="repeat(5, 1fr)" gap={5}>
                  <AddCollectionButton
                    width="168px"
                    height="208px"
                    labelWidth="130px"
                    label="Add New Trait"
                    labelDescription="Choose a folder or Drop it here."
                    onClick={open}
                  />

                  {files.map((file, index) =>
                    !file.isDelete ? (
                      <ScaleFade
                        key={index}
                        initialScale={0.9}
                        in={!file.isDelete}
                        reverse
                      >
                        <UploadAttributeCard
                          file={file}
                          onDelete={() => {
                            onDeleteTraitClick(index, true);
                          }}
                          onChange={(value) => changeRarityWeight(index, value)}
                        />
                      </ScaleFade>
                    ) : (
                      <SlideFade
                        key={index}
                        reverse
                        delay={0.1}
                        in={file.isDelete}
                      >
                        <DeleteTraitCard
                          name={file.name}
                          onClose={(value) =>
                            onConfirmDeleteTrait(value, index)
                          }
                        />
                      </SlideFade>
                    )
                  )}
                </Grid>
              )}
              {files.length === 0 && (
                <Flex
                  cursor={"pointer"}
                  direction="column"
                  h="100%"
                  justifyContent={"center"}
                  alignItems={"center"}
                  onClick={open}
                >
                  <ImageChakra
                    src="/add.png"
                    w="78px"
                    h="78px"
                    mr="0px"
                    objectFit={"contain"}
                  />
                  <FormLabel
                    fontSize={"28px"}
                    name="upload"
                    mr="0px"
                    mt="28px"
                    textAlign={"center"}
                    text="Start Adding Traits"
                  />

                  <FormDescription
                    textAlign={"center"}
                    fontSize={"16px"}
                    mr="0px"
                    text="Select files or drag and drop here."
                  />
                  <FormDescription
                    textAlign={"center"}
                    fontSize={"12px"}
                    mr="0px"
                    width="470px"
                    mt="40px"
                    text="Upload the associated traits (files) for your attribute. (e.g., all the different eye colours traits (files) will be added in the Eye Colour attribute folder.)"
                  />
                  <FormDescription
                    textAlign={"center"}
                    fontSize={"12px"}
                    mr="0px"
                    mt="20px"
                    mb='0px'
                    width="400px"
                    text="Image size on all traits must be the same."
                  />
                  <FormDescription
                    textAlign={"center"}
                    fontSize={"12px"}
                    mr="0px"
                    width="400px"
                    text="File type: PNG files with transparent background."
                  />
                </Flex>
              )}
            </ModalBody>
          </Box>
        </ModalContent>
      </Modal>
    </>
  );
}

export default UploadAttributeModal;
