import {
  Center,
  Divider,
  Flex,
  Modal,
  ModalBody,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Spinner,
  Tab,
  TabList,
  TabPanel,
  TabPanels,
  Tabs,
  Text,
} from "@chakra-ui/react";
import React, { useContext } from "react";
import { Collection } from "../../../system/api/types";
import { CollectorContext } from "../../../system/context/CollectorContext";
import useDeviceDetect from "../../../system/hooks/useDeviceDetect";
import { useUiText } from "../../../system/hooks/useUiText";
import {
  APP_DIVIDER_COLOR,
  APP_PRIMARY_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_SECONDARY_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  WALLET_MODAL_BODY_BG_COLOR,
  WALLET_MODAL_HEADER_BG_COLOR,
} from "../../../theme/colors";
import { CollectionContractProvider } from "./CollectionContractContext";
import { CollectionDetails } from "./CollectionDetails";
import { CollectionPreMintView } from "./CollectionPreMintView";
import { CollectionProvenanceView } from "./CollectionProvenanceView";
import { CollectionRevealView } from "./CollectionRevealView";
import { MyCollectionsSection } from "./MyCollectionsSection";
import { MyReceiptsSection } from "./MyReceiptsSection";
import { WalletModalProps } from "./types";
import { WalletModalChangeNetworkView } from "./WalletModalChangeNetworkView";
import { WalletModalConnectView } from "./WalletModalConnectView";
import { WalletModalTransactionFailedView } from "./WalletModalTransactionFailedView";
import { WalletModalTransactionPendingView } from "./WalletModalTransactionPendingView";
import { WalletModalTransactionSuccessfulView } from "./WalletModalTransactionSuccessfulView";

export const WalletModal = ({
  account,
  accountChangePending,
  balance,
  clearSelectedCollection,
  collections,
  handleClose,
  handleConnect,
  handleDisconnect,
  handleSelectCollection,
  handleUpdateCollections,
  handleViewPreMint,
  handleViewReveal,
  handleViewProvenance,
  hasReceipt,
  insufficientFunds,
  isOpen,
  isWrongNetwork,
  onClose,
  selectedCollection,
  switchNetwork,
  tokenIds,
  transactionPending,
  transactionSuccessful,
  transactionUnSuccessful,
  viewPreMint,
  viewReveal,
  viewProvenance,
  walletModalTabIndex,
}: WalletModalProps) => {
  const { uiText } = useUiText();
  const [tabIndex, setTabIndex] = React.useState(0);
  React.useEffect(() => {
    setTabIndex(walletModalTabIndex);
  }, [walletModalTabIndex]);
  const { isMintPage } = useContext(CollectorContext);
  const { isMobile } = useDeviceDetect();
  const isMobileMint = isMobile && isMintPage;

  const renderModalContentView = () => {
    return (
      <>
        {!selectedCollection && (
          <>
            <ModalHeader
              mx={"-2px"}
              mt={"-2px"}
              borderWidth={"1px"}
              borderTopRadius={"12px"}
              borderColor={WALLET_MODAL_HEADER_BG_COLOR}
              bgColor={WALLET_MODAL_HEADER_BG_COLOR}
              color={APP_PRIMARY_CONTRAST_COLOR}
            >
              <Flex
                flexDir={"row"}
                alignItems={"center"}
                justifyContent={"space-between"}
              >
                <Flex flexDir={"column"}>
                  <Text
                    fontSize="12px"
                    fontFamily={"heading"}
                    fontWeight="semibold"
                  >
                    {uiText.walletAddress}
                  </Text>
                  <Text
                    fontFamily={"body"}
                    fontWeight={"normal"}
                    whiteSpace={"nowrap"}
                    overflow={"hidden"}
                    textOverflow={"ellipsis"}
                    fontSize="12px"
                    w="161px"
                  >
                    {account}
                  </Text>
                </Flex>
                <Text
                  as="button"
                  fontFamily={"heading"}
                  fontSize="12px"
                  fontWeight="semibold"
                  _hover={{ opacity: 0.7 }}
                  onClick={handleDisconnect}
                >
                  {uiText.disconnect}
                </Text>
              </Flex>
            </ModalHeader>
            <Divider color={APP_DIVIDER_COLOR} />
            <ModalBody
              p="5px 5px 0px 15px"
              color={"#000"}
              bgColor={WALLET_MODAL_BODY_BG_COLOR}
              borderBottomRadius="8px"
            >
              <Tabs
                variant="line"
                isLazy
                h="95%"
                position="relative"
                defaultIndex={tabIndex}
              >
                <TabList borderBottom="none">
                  {[uiText.myReceipts, uiText.collections].map((subHeading) => (
                    <Tab
                      key={`sub-heading-${subHeading}`}
                      _selected={{
                        borderBottomWidth: "3px",
                        borderColor: APP_SECONDARY_COLOR,
                        color: APP_PRIMARY_COLOR,
                        fontWeight: 600,
                      }}
                      _focus={{ boxShadow: "none" }}
                      _active={{ background: "none" }}
                      fontSize="20px"
                      fontFamily={"heading"}
                      fontWeight={"semibold"}
                      color={APP_TEXT_INACTIVE_COLOR}
                      mr="40px"
                      mb="20px"
                    >
                      {subHeading}
                    </Tab>
                  ))}
                </TabList>
                <TabPanels zIndex="2">
                  <TabPanel h="455px">
                    <MyReceiptsSection
                      receipts={tokenIds}
                      balance={balance}
                      insufficientFunds={insufficientFunds}
                    />
                  </TabPanel>
                  <TabPanel h="455px" bg={APP_PRIMARY_CONTRAST_COLOR}>
                    <MyCollectionsSection
                      hasReceipt={hasReceipt}
                      collections={
                        collections.sort(function (
                          a: Collection,
                          b: Collection
                        ) {
                          return (
                            new Date(b.dateCreated).valueOf() -
                            new Date(a.dateCreated).valueOf()
                          );
                        }) || []
                      }
                      setCollection={handleSelectCollection}
                      updateCollections={handleUpdateCollections}
                    />
                  </TabPanel>
                </TabPanels>
              </Tabs>
            </ModalBody>
          </>
        )}
      </>
    );
  };

  const renderLoadingState = () => {
    return (
      <Center h="100%">
        <Spinner color={APP_SECONDARY_COLOR} />
      </Center>
    );
  };

  function renderWithModal<P>(
    Component: React.FC<P>,
    props: P,
    transparent?: boolean,
    collectionAddress?: string
  ) {
    return isMobileMint ? (
      <Modal
        isOpen={isOpen}
        onClose={handleClose}
        scrollBehavior="inside"
        motionPreset="slideInBottom"
      >
        <ModalOverlay />
        <ModalContent
          minH={"full"}
          borderRadius={"12px"}
          borderWidth={"1px"}
          borderColor={"transparent"}
          bg={transparent ? "transparent" : WALLET_MODAL_BODY_BG_COLOR}
        >
          {!!collectionAddress ? (
            <CollectionContractProvider contractAddress={collectionAddress}>
              <Component {...props} />
            </CollectionContractProvider>
          ) : (
            <Component {...props} />
          )}
        </ModalContent>
      </Modal>
    ) : (
      <Modal
        isOpen={isOpen}
        onClose={handleClose}
        scrollBehavior="inside"
        isCentered={isMintPage}
      >
        <ModalOverlay />
        <ModalContent
          maxWidth="388px"
          height="663px"
          borderRadius={"12px"}
          borderWidth={"1px"}
          borderColor={"transparent"}
          bg={transparent ? "transparent" : WALLET_MODAL_BODY_BG_COLOR}
          position={isMintPage ? undefined : "absolute"}
          top={isMintPage ? undefined : 3}
          right={isMintPage ? undefined : "85px"}
        >
          {!!collectionAddress ? (
            <CollectionContractProvider contractAddress={collectionAddress}>
              <Component {...props} />
            </CollectionContractProvider>
          ) : (
            <Component {...props} />
          )}
        </ModalContent>
      </Modal>
    );
  }

  if (isWrongNetwork) {
    return renderWithModal(
      WalletModalChangeNetworkView,
      {
        handleReconnect: switchNetwork,
      },
      true
    );
  }

  if (transactionPending) {
    return renderWithModal(WalletModalTransactionPendingView, {}, true);
  }

  if (transactionSuccessful) {
    return renderWithModal(WalletModalTransactionSuccessfulView, {}, true);
  }

  if (transactionUnSuccessful) {
    return renderWithModal(WalletModalTransactionFailedView, {}, true);
  }

  if (viewPreMint) {
    return renderWithModal(
      CollectionPreMintView,
      {
        goBack: () => handleViewPreMint(false),
      },
      false,
      selectedCollection?.contractAddress
    );
  }

  if (viewProvenance) {
    return renderWithModal(
      CollectionProvenanceView,
      {
        goBack: () => handleViewProvenance(false),
        goToEnableMinting: () => handleViewPreMint(true),
        collection: selectedCollection as Collection,
      },
      false,
      selectedCollection?.contractAddress
    );
  }

  if (viewReveal) {
    return renderWithModal(
      CollectionRevealView,
      {
        goBack: () => handleViewReveal(false),
        collection: selectedCollection as Collection,
      },
      false,
      selectedCollection?.contractAddress
    );
  }

  if (!account && !accountChangePending) {
    return renderWithModal(WalletModalConnectView, {
      handleConnect,
      onClose,
    });
  }

  if (selectedCollection) {
    return renderWithModal(
      CollectionDetails,
      {
        collection: selectedCollection as Collection,
        onBackToCollections: () => {
          clearSelectedCollection();
          setTabIndex(1);
        },
        goToEnableMinting: handleViewPreMint,
        goToProvenance: handleViewProvenance,
        goToRevealCollection: handleViewReveal,
      },
      false,
      selectedCollection.contractAddress
    );
  }

  return (
    <Modal
      isOpen={isOpen}
      onClose={handleClose}
      scrollBehavior="inside"
      isCentered={isMintPage}
    >
      <ModalOverlay />
      <ModalContent
        maxWidth="388px"
        height="663px"
        borderRadius={"12px"}
        borderWidth={"2px"}
        borderColor={"transparent"}
        position={isMintPage ? undefined : "absolute"}
        top={isMintPage ? undefined : 3}
        right={isMintPage ? undefined : "85px"}
      >
        {accountChangePending && renderLoadingState()}
        {account &&
          !accountChangePending &&
          !isMintPage &&
          renderModalContentView()}
      </ModalContent>
    </Modal>
  );
};
