import React from "react";
import { useNavigate } from "react-router-dom";
import { ContractContext } from "../../../system/context/ContractContext";
import { ModalContext } from "../../../system/context/ModalContext";
import { WalletContext } from "../../../system/context/WalletContext";

export const useNoReceipt = () => {
  const { hasReceipt } = React.useContext(ContractContext);
  const { account } = React.useContext(WalletContext);
  const { noReceiptsModal, walletModal } = React.useContext(ModalContext);
  let navigate = useNavigate();
  const handleCreateNewCollection = () => {
    if (!account) {
      walletModal.onOpen();
      return;
    }

    if (hasReceipt) {
      navigate("/create-collection");
    } else {
      noReceiptsModal.onOpen();
    }
  };

  return {
    handleCreateNewCollection,
  };
};
