import { useFieldArray, useForm } from "react-hook-form";
import {
  chakra,
  Flex,
  FormErrorMessage,
  FormControl,
  Input,
  Textarea,
  Button,
  Image as ImageChakra,
  Grid,
  Tooltip,
  useDisclosure,
  Box,
  Heading,
  Text,
} from "@chakra-ui/react";
import { saveAs } from "file-saver";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import Axios from "axios";
import { useUiText } from "../../system/hooks/useUiText";
import FormLabel from "../../components/forms/FormLabel";
import FormDescription from "../../components/forms/FormDescription";
import { PrimaryButton } from "../../components/buttons";
import AddCollectionButton from "../../components/buttons/AddCollectionButton";
import UploadAttributeModal from "../../components/modals/UploadAttributeModal";
import {
  AttributeType,
  CollectionFormDataType,
  DropFileType,
} from "../../types";
import { useCallback, useEffect, useRef, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import {
  APP_BORDER_COLOR_DARK,
  APP_DESCRIPTION_COLOR,
  APP_DIVIDER_COLOR,
  APP_INFO_BACKGROUND_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_SECONDARY_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  APP_WARNING_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";
import { FileWithPath, useDropzone } from "react-dropzone";
import FileErrorModal from "../../components/modals/FileErrorModal";
import AttributeCardContainer from "../../containers/AttributeCardContainer";

function CreateCollection() {
  const navigate = useNavigate();
  let location = useLocation();
  const props: CollectionFormDataType = location.state;
  const errorAttributesRef = useRef<null | HTMLDivElement>(null);

  const { isOpen, onOpen, onClose } = useDisclosure();
  const [uploadedAttributes, setUploadedAttributes] = useState(
    [] as AttributeType[]
  );
  const [showFileReject, setShowFileReject] = useState(false);

  const [attributesError, setAttributesError] = useState("");
  const [imageWidth, setImageWidth] = useState(0);
  const [imageHeight, setImageHeight] = useState(0);
  const [preview, setPreview] = useState("");
  const [isPreviewLoading, setIsPreviewLoading] = useState(false);
  const [blobPreview, setBlobPreview] = useState({} as Blob);
  const [selectedAttributes, setSelectedAttributes] = useState(
    {} as AttributeType
  );
  const { uiText } = useUiText();
  const {
    handleSubmit,
    control,
    register,
    formState: { errors, isSubmitting },
  } = useForm<CollectionFormDataType>();

  const { fields, append, remove, prepend } = useFieldArray({
    control,
    name: "metadata",
  });

  useEffect(() => {
    if (props) {
      setUploadedAttributes(props.attributes!);
    }
  }, [props, uploadedAttributes]);

  useEffect(() => {
    if (props) {
      props.metadata!.map((data) =>
        prepend({ key: data.key, value: data.value }, { shouldFocus: false })
      );
    } else {
      prepend({ key: "", value: "" }, { shouldFocus: false });
    }
  }, [props, prepend]);

  const scrollTo = (ref: React.MutableRefObject<HTMLDivElement | null>) => {
    if (!ref.current) return;
    ref.current.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };

  const onDrop = useCallback(
    (acceptedFiles: FileWithPath[]) => {
      const fileChecker = acceptedFiles.filter(
        (file) => file.path!.split("/").length !== 1
      );
      console.log("filechecker", fileChecker);
      const renamed = fileChecker.map((file) => {
        const pathLength = file.path!.split("/").length;
        const hashtag = file.name.search("#") !== -1;
        const fileName = file.name.split(hashtag ? "#" : ".")[0];
        const rarity = hashtag ? file.name.split("#")[1].split(".")[0] : "1";
        const newFile = new File([file], `${fileName}#${rarity}.png`, {
          type: file.type,
        });
        return Object.assign(newFile, {
          folderName: file.path!.split("/")[pathLength > 3 ? 2 : 1],
          path: file.path,
          rarityWeight: rarity,
        });
      }) as DropFileType[];

      const grouppingObjects: { [name: string]: DropFileType[] } = groupBy(
        renamed,
        "folderName"
      );
      const newAttributes = [...uploadedAttributes];
      Object.keys(grouppingObjects).map((name) =>
        newAttributes.push({ name, files: grouppingObjects[name] })
      );
      setUploadedAttributes(newAttributes);
    },
    [uploadedAttributes]
  );

  function groupBy(arr: DropFileType[], property: string) {
    return arr.reduce(function (
      memo: { [name: string]: DropFileType[] },
      x: { [name: string]: any }
    ) {
      if (!memo[x[property]]) {
        memo[x[property]] = [];
      }
      memo[x[property]].push(x as DropFileType);
      return memo;
    },
    {});
  }

  const { getRootProps, getInputProps, fileRejections } = useDropzone({
    onDrop,
    noClick: true,
    accept: "image/png",
    maxSize: 10485760,
  });

  useEffect(() => {
    if (fileRejections.length) {
      setShowFileReject(true);
    }
  }, [fileRejections]);

  useEffect(() => {
    if (uploadedAttributes.length) {
      const image = new Image();
      let url: string;
      image.onload = function () {
        setImageHeight(image.height);
        setImageWidth(image.width);
      };
      url = URL.createObjectURL(uploadedAttributes[0].files[0]);
      image.src = url;
    }
  }, [uploadedAttributes]);

  function onSubmit(values: CollectionFormDataType) {
    if (uploadedAttributes.length < 2) {
      setAttributesError("Minimum of two attributes needed");
      scrollTo(errorAttributesRef);
    } else {
      setAttributesError("");
      navigate("/review-collection", {
        state: { ...values, attributes: uploadedAttributes },
        replace: true,
      });
    }
  }

  const countDecimals = function (value: string) {
    let text = value.toString();
    // verify if number 0.000005 is represented as "5e-6"
    if (text.indexOf("e-") > -1) {
      let [trail] = text.split("e-");
      let deg = parseInt(trail, 10);
      return deg;
    }
    // count decimals for number in representation like "0.123456"
    if (Math.floor(Number(value)) !== Number(value)) {
      return value.toString().split(".")[1]?.length || 0;
    }
    return 0;
  };

  const onOpenModal = (index: number) => {
    setSelectedAttributes({ ...uploadedAttributes[index], index });
    onOpen();
  };

  const onCloseModal = (
    attributeName: string,
    attributes: DropFileType[],
    index?: number
  ) => {
    if (!!attributeName && attributes.length) {
      if (
        // Check if update
        index !== undefined
      ) {
        const array = uploadedAttributes;
        array[index!].name = attributeName;
        array[index!].files = attributes;
        setUploadedAttributes(array);
      } else {
        const data = { name: attributeName, files: attributes };
        const newAttributes = uploadedAttributes;
        newAttributes.push(data);
        setUploadedAttributes(newAttributes);
      }
    }
    setSelectedAttributes({} as AttributeType);
    onClose();
  };

  const onDeleteAttribute = (index?: number) => {
    if (index !== undefined) {
      const attributes = uploadedAttributes;
      attributes.splice(index, 1);
      setUploadedAttributes(attributes);
      setSelectedAttributes({} as AttributeType);
      onClose();
    } else {
      setSelectedAttributes({} as AttributeType);
      onClose();
    }
  };

  const generatePreview = async () => {
    setIsPreviewLoading(true);
    const https = require("https");
    const httpsAgent = new https.Agent({ rejectUnauthorized: false });
    const previewID = new Date().getTime() + Math.random();
    // Form Data
    let formData = new FormData();
    formData.append("id", String(previewID));
    formData.append("isPreview", "true");
    formData.append("imageHeight", String(imageHeight));
    formData.append("imageWidth", String(imageWidth));
    uploadedAttributes.map((attribute) => {
      const filename = attribute.name;
      const files = attribute.files;
      return files.map((file) => {
        return formData.append(`${filename}`, file);
      });
    });

    await Axios({
      method: "POST",
      httpsAgent,
      url: `https://${process.env.REACT_APP_SERVER_URL}.com/make`,
      data: formData,
      responseType: "blob",
      headers: { "Content-Type": "multipart/form-data" },
    })
      .then((res) => {
        setPreview(URL.createObjectURL(res.data));
        setBlobPreview(res.data);
        setIsPreviewLoading(false);
        return res;
      })
      .catch((err) => {
        setIsPreviewLoading(false);
        if (!err.response) {
          // network error
          return Promise.reject("Error: Network Error");
        } else {
          const newBlob = new Blob([err.response.data], { type: "text/html" });
          newBlob.text().then((res) => {
            if (res.search("Error: Input stream problem, please retry") !== -1)
              return generatePreview();
          });
          return Promise.reject(err);
        }
      });
  };

  const saveImage = () => {
    const blob = new Blob([blobPreview], { type: "image/png" });
    saveAs(blob, "preview.png");
  };

  return (
    <>
      <Flex flexDir="column" alignItems="flex-start" px={181} pb={181} w="100%">
        <UploadAttributeModal
          name={selectedAttributes.name}
          attributes={selectedAttributes.files}
          index={selectedAttributes.index}
          isOpen={isOpen}
          onClose={onCloseModal}
          onOpen={onOpen}
          onDeleteAttribute={onDeleteAttribute}
        />
        {showFileReject && (
          <FileErrorModal
            onClose={() => setShowFileReject(false)}
            filenames={fileRejections.map((data) => data.file.name)}
          />
        )}
        {/* Create Collection Title */}
        <Heading
          textAlign="left"
          maxW={1000}
          fontWeight={"medium"}
          fontSize={{ base: "24px", md: "44px", lg: "44px" }}
          my={"50px"}
        >
          {uiText.createCollectionTitle}
        </Heading>
        {/* Form Start */}
        <form style={{ width: "100%" }} onSubmit={handleSubmit(onSubmit)}>
          {/* Collection Name */}
          <FormControl isInvalid={!!errors.name}>
            <FormLabel name="name" text={uiText.createCollectionNameLabel} />
            <FormDescription text={uiText.createCollectionNameDescription} />
            <Input
              id="name"
              size={"lg"}
              placeholder={uiText.createCollectionPlaceholder30Characters}
              {...register("name", {
                value: props ? props.name : "",
                shouldUnregister: false,
                required: uiText.createCollectionRequired,
                maxLength: {
                  value: 30,
                  message: uiText.createCollectionMax30Characters,
                },
              })}
            />
            <FormErrorMessage>
              {errors.name && errors.name.message}
            </FormErrorMessage>
          </FormControl>

          {/* Collection Symbol */}
          <FormControl mt="40px" isInvalid={!!errors.symbol}>
            <FormLabel
              name="symbol"
              text={uiText.createCollectionSymbolLabel}
            />
            <FormDescription text={uiText.createCollectionSymbolDescription} />
            <Input
              id="symbol"
              size={"lg"}
              placeholder={uiText.createCollectionPlaceholderMax10Characters}
              {...register("symbol", {
                value: props ? props.symbol : "",
                required: uiText.createCollectionRequired,
                maxLength: {
                  value: 10,
                  message: uiText.createCollectionMax10Characters,
                },
              })}
            />
            <FormErrorMessage>
              {errors.symbol && errors.symbol.message}
            </FormErrorMessage>
          </FormControl>

          {/* Collection Description */}
          <FormControl
            ref={errorAttributesRef}
            mt="40px"
            isInvalid={!!errors.description}
          >
            <FormLabel
              name="description"
              text={uiText.createCollectionDescriptionLabel}
            />
            <FormDescription
              text={uiText.createCollectionDescriptionDescription}
            />
            <Textarea
              id="description"
              size={"lg"}
              h="118px"
              resize={"none"}
              maxLength={150}
              textAlign="start"
              placeholder={uiText.createCollectionPlaceholderMax150Characters}
              {...register("description", {
                value: props ? props.description : "",
                maxLength: {
                  value: 150,
                  message: uiText.createCollectionMax150Characters,
                },
              })}
            />
            <FormErrorMessage>
              {errors.description && errors.description.message}
            </FormErrorMessage>
          </FormControl>

          {/* Upload Artwork */}
          <FormControl
            mt="40px"
            isInvalid={!!errors.attributes}
            style={{ outline: "none" }}
            {...getRootProps()}
          >
            <input {...getInputProps()} />
            <Flex flexDirection={"row"}>
              <FormLabel
                name="upload"
                text={uiText.createCollectionGenerateLabel}
              />
              <Tooltip
                placement="top"
                hasArrow
                bg={APP_INFO_BACKGROUND_COLOR}
                borderWidth={"0.5px"}
                borderRadius={"4px"}
                fontSize={"10px"}
                p="20px"
                w="180px"
                label={uiText.createCollectionGenerateTooltip}
              >
                <ImageChakra
                  src="/tooltip.png"
                  w="20px"
                  h="20px"
                  marginLeft={"20px"}
                  objectFit={"contain"}
                />
              </Tooltip>
            </Flex>
            <FormDescription
              width="650px"
              fontSize={"16px"}
              text={uiText.createCollectionGenerateDescription}
            />

            <chakra.h2
              textAlign={"left"}
              color={APP_WARNING_COLOR}
              fontSize={"14px"}
            >
              {attributesError}
            </chakra.h2>

            {/* Preview Attribute */}
            <Grid marginTop="46px" templateColumns="repeat(5, 1fr)" gap={5}>
              <DndProvider backend={HTML5Backend}>
                <AttributeCardContainer
                  attributes={uploadedAttributes}
                  onOpenModal={onOpenModal}
                  setAttributes={setUploadedAttributes}
                />
              </DndProvider>
              {uploadedAttributes.length < 20 && (
                <Box outline={"none"}>
                  <AddCollectionButton
                    onClick={onOpen}
                    label={uiText.createCollectionGenerateButtonLabel}
                    labelDescription={
                      uiText.createCollectionGenerateButtonDescription
                    }
                  />
                </Box>
              )}
            </Grid>
            <Flex
              flexDir={"row"}
              width={"100%"}
              justifyContent={"center"}
              alignItems={"center"}
              fontSize={"12px"}
              color={
                uploadedAttributes.length < 2
                  ? APP_TEXT_INACTIVE_COLOR
                  : HOMEPAGE_BACKGROUND_COLOR
              }
              mt={"50px"}
              letterSpacing={"2.4px"}
            >
              <Box
                borderColor={APP_SECONDARY_COLOR}
                borderWidth={"0.5px"}
                width="100%"
              />
              <chakra.h1 ml={"40px"}>
                {uiText.createCollectionPreviewTip1}
              </chakra.h1>
              <chakra.h1 width="600px" fontSize={"16px"} mx={"60px"}>
                {uiText.createCollectionPreviewTip2}
              </chakra.h1>
              <chakra.h1 mr={"40px"}>
                {uiText.createCollectionPreviewTip3}
              </chakra.h1>
              <Box
                borderColor={APP_SECONDARY_COLOR}
                borderWidth={"0.5px"}
                width="100%"
              />
            </Flex>
          </FormControl>

          {/* Generate Preview */}
          <Flex
            flexDir={"row"}
            my={"60px"}
            justifyContent={"center"}
            alignItems={"center"}
          >
            <Flex flexDir={"column"} flex={1}>
              <Flex
                flexDir={"row"}
                justifyContent={"center"}
                alignItems={"center"}
                mb={"40px"}
              >
                <Button
                  disabled={
                    (props && props.attributes?.length === 0) ||
                    uploadedAttributes.length < 2 ||
                    isPreviewLoading
                  }
                  onClick={generatePreview}
                  isLoading={isPreviewLoading}
                  color={HOMEPAGE_BACKGROUND_COLOR}
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                  borderColor={HOMEPAGE_BACKGROUND_COLOR}
                  borderRadius={"27px"}
                  borderWidth={"1px"}
                  fontSize={"16px"}
                  height={"48px"}
                  width="100%"
                >
                  <Heading fontSize={"16px"} fontWeight={"semibold"}>
                    Generate Preview
                  </Heading>
                </Button>
                <Button
                  disabled={
                    (props && props.attributes?.length === 0) ||
                    uploadedAttributes.length < 2 ||
                    isPreviewLoading
                  }
                  marginLeft={"22px"}
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                  onClick={generatePreview}
                >
                  <ImageChakra
                    src="/reload.png"
                    w="22px"
                    h="22px"
                    objectFit={"contain"}
                  />
                </Button>
              </Flex>
              <Flex flexDir={"row"} justifyContent={"start"} ml="34px">
                <Button
                  onClick={saveImage}
                  disabled={!preview}
                  fontSize={"12px"}
                  backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
                >
                  <ImageChakra
                    src="/download.png"
                    w="22px"
                    h="22px"
                    marginRight={"20px"}
                    objectFit={"contain"}
                  />
                  {uiText.createCollectionDownloadLabel}
                </Button>
              </Flex>
            </Flex>
            <Flex ml="50px" flex={2}>
              {preview && (
                <ImageChakra
                  loading={"eager"}
                  src={preview}
                  height={"280px"}
                  objectFit={"contain"}
                />
              )}
              {!preview && (
                <Box
                  height={"280px"}
                  display={"flex"}
                  bgColor={APP_DIVIDER_COLOR}
                  width={"100%"}
                  justifyContent={"center"}
                  alignItems={"center"}
                >
                  <ImageChakra src="/image.png" />
                </Box>
              )}
            </Flex>
          </Flex>

          {/* NFT Cost */}
          <FormControl mt="40px" isInvalid={!!errors.cost}>
            <FormLabel name="cost" text={uiText.createCollectionCostLabel} />
            <FormDescription text={uiText.createCollectionCostDescription} />
            <Input
              id="cost"
              size={"lg"}
              textAlign="start"
              onWheel={(e) => e.currentTarget.blur()}
              step={"any"}
              onKeyPress={(event) => {
                if (!/^\d*\.?\d*$/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              placeholder={uiText.createCollectionCostPlaceholder}
              {...register("cost", {
                value: props ? props.cost : "",
                validate: (value) => {
                  return (
                    countDecimals(value) <= 18 ||
                    uiText.createCollectionDecimalsError
                  );
                },
                required: uiText.createCollectionRequired,
                maxLength: {
                  value: 30,
                  message: uiText.createCollectionMax30Characters,
                },
              })}
            />
            <FormErrorMessage>
              {errors.cost && errors.cost.message}
            </FormErrorMessage>
          </FormControl>

          {/* Total Item */}
          <FormControl mt="40px" isInvalid={!!errors.total}>
            <FormLabel name="total" text={uiText.createCollectionTotalLabel} />
            <FormDescription text={uiText.createCollectionTotalDescription} />
            <Input
              id="total"
              size={"lg"}
              type="number"
              onWheel={(e) => e.currentTarget.blur()}
              placeholder={uiText.createCollectionMax10000}
              {...register("total", {
                value: props ? props.total : "",
                required: uiText.createCollectionRequired,
                max: {
                  value: 10000,
                  message: uiText.createCollectionMetadataError,
                },
              })}
            />
            <FormErrorMessage>
              {errors.total && errors.total.message}
            </FormErrorMessage>
          </FormControl>

          <Heading
            fontWeight={"semibold"}
            textAlign="left"
            fontSize={"20px"}
            mt={"120px"}
          >
            {uiText.createCollectionMetadataLabel}
          </Heading>
          <Text
            mt="9px"
            fontWeight={"normal"}
            textAlign="left"
            fontSize={"16px"}
            color={APP_DESCRIPTION_COLOR}
          >
            {uiText.createCollectionMetadataDescription1}
            <br /> {uiText.createCollectionMetadataDescription2}
          </Text>

          <Grid templateColumns="repeat(2, 1fr)" gap={6}>
            <Box>
              <FormLabel mt={"30px"} name="key" text="Key" />
              <FormDescription text={uiText.createCollectionMetadataExample} />
            </Box>
            <Box>
              <FormLabel mt={"30px"} name="value" text="Value" />
              <FormDescription text={uiText.createCollectionValueExample} />
            </Box>
          </Grid>
          {/* Key */}
          {fields.map((item, index) => (
            <Grid key={item.id} templateColumns="repeat(2, 1fr)" gap={6}>
              <FormControl
                mt="12px"
                display={"flex"}
                flexDir={"column"}
                alignItems={"flex-start"}
              >
                <Input
                  id="key"
                  size={"lg"}
                  placeholder={uiText.createCollectionPlaceholder30Characters}
                  {...register(`metadata.${index}.key`, {
                    maxLength: {
                      value: 30,
                      message: uiText.createCollectionMax30Characters,
                    },
                  })}
                />
                <FormErrorMessage>
                  {errors.metadata &&
                    errors.metadata[index].key &&
                    errors.metadata[index].key!.message}
                </FormErrorMessage>
              </FormControl>

              {/* Value */}
              <FormControl
                mt="12px"
                display={"flex"}
                flexDir={"column"}
                alignItems={"flex-start"}
              >
                <Flex direction={"row"} w="100%">
                  <Input
                    id="value"
                    size={"lg"}
                    placeholder={uiText.createCollectionPlaceholder30Characters}
                    {...register(`metadata.${index}.value`, {
                      maxLength: {
                        value: 30,
                        message: uiText.createCollectionMax30Characters,
                      },
                    })}
                  />
                  {fields.length > 1 && (
                    <Button
                      onClick={() => remove(index)}
                      backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
                      ml="8px"
                    >
                      <ImageChakra
                        src="/delete.png"
                        maxH={[400, 400, 400, 550, 700, 700]}
                      />
                    </Button>
                  )}
                </Flex>
                <FormErrorMessage>
                  {errors.metadata &&
                    errors.metadata[index].value &&
                    errors.metadata[index].value!.message}
                </FormErrorMessage>
              </FormControl>
            </Grid>
          ))}

          <Box display={"flex"} alignItems={"flex-start"} mt="24px">
            <Button
              onClick={() => append({ key: "", value: "" })}
              borderRadius={"15px"}
              w="105px"
              h="29px"
              backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
              borderColor={APP_BORDER_COLOR_DARK}
              borderWidth={"0.5px"}
              display={"flex"}
              justifyContent={"center"}
              alignItems={"center"}
            >
              <span
                style={{
                  fontSize: "25px",
                  fontWeight: "normal",
                  marginTop: "-3px",
                }}
              >
                +
              </span>
              <Heading fontSize={"13px"} ml="8px" fontWeight={"semibold"}>
                {uiText.createCollectionAddMetadata}
              </Heading>
            </Button>
          </Box>

          {/* Submit Button */}
          <PrimaryButton
            disabled={isPreviewLoading}
            mt={"100px"}
            w={537}
            isLoading={isSubmitting}
            type="submit"
            color={HOMEPAGE_BACKGROUND_COLOR}
            text={uiText.createCollectionSubmitButton}
          />
        </form>
        {/* Form End */}
      </Flex>
    </>
  );
}

export default CreateCollection;
