import { Button, Text } from "@chakra-ui/react";
import { useContext } from "react";
import { FaWallet } from "react-icons/fa";
import { CollectorContext } from "../../system/context/CollectorContext";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_HEADER_BACKGROUND_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_SECONDARY_COLOR,
} from "../../theme/colors";
import { ConnectWalletButtonProps } from "./types";

export const ConnectWalletButton = ({
  onClick,
  isConnected,
  connectPending,
}: ConnectWalletButtonProps) => {
  const { uiText } = useUiText();
  const { isMintPage } = useContext(CollectorContext);
  return (
    <Button
      p={1}
      disabled={isConnected && isMintPage}
      _disabled={{ opacity: 100, cursor: "default" }}
      color={APP_SECONDARY_COLOR}
      rightIcon={<FaWallet />}
      onClick={onClick}
      variant="ghost"
      _hover={{ backgroundColor: APP_HEADER_BACKGROUND_COLOR }}
      _active={{ bg: "none" }}
      _focus={{ borderColor: "white" }}
      isLoading={connectPending}
    >
      <Text
        display={isMintPage ? "block" : ["none", "none", "block"]}
        fontSize={["12px", "12px", "16px"]}
        fontFamily="body"
        fontWeight={"semibold"}
        color={isConnected ? APP_SECONDARY_COLOR : APP_PRIMARY_CONTRAST_COLOR}
        mr={["8px", "8px", 5]}
      >
        {isConnected ? `Connected` : uiText.connectWallet}
      </Text>
    </Button>
  );
};
