import {
  Box,
  Flex,
  IconButton,
  Image as ImageChakra,
  Input,
  Text,
  Tooltip,
} from "@chakra-ui/react";
import { useEffect, useState } from "react";
import CopyToClipboard from "react-copy-to-clipboard";
import { FileWithPath } from "react-dropzone";
import { MdContentCopy } from "react-icons/md";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { PrimaryButton } from "../../components/buttons";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_IMAGE_PLACEHOLDER_COLOR,
  APP_PRIMARY_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  APP_WARNING_COLOR,
  DESCRIPTION_COLOR,
  UPLOAD_BOX_TEXT_COLOR,
} from "../../theme/colors";

import Dropzone from "react-dropzone";
import { CustomizeMintProps } from "./types";
import { Collection } from "../../system/api/types";
import { getMintPageContent, updateMintPage } from "../../system/api/api";
import { v4 } from "uuid";
import { useEthereumWallet } from "../../system/hooks/ethereum/useEthereumWallet";
import useDeviceDetect from "../../system/hooks/useDeviceDetect";
import { UpdatedNotificationModal } from "../Modals/UpdatedNotificationModal/UpdatedNotificationModal";
import { ErrorNotificationModal } from "../Modals/ErrorNotificationModal/ErrorNotificationModal";

function CustomizeMint() {
  const { uiText } = useUiText();
  let location = useLocation();
  const { sign, account } = useEthereumWallet();
  const props: CustomizeMintProps = location.state;
  let navigate = useNavigate();
  const { address } = useParams();

  const { isBigScreen } = useDeviceDetect();

  const [copyButtonTooltip, setCopyButtonTooltip] = useState<string>(
    uiText.copyButtonTooltip
  );

  const [desktopImage, setDesktopImage] = useState<FileWithPath>(null as any);
  const [mobileImage, setMobileImage] = useState<FileWithPath>(null as any);
  const [discord, setDiscord] = useState("");
  const [instagram, setInstagram] = useState("");
  const [twitter, setTwitter] = useState("");
  const [opensea, setOpensea] = useState("");
  // const [telegram, setTelegram] = useState(""); // TODO - hook this up
  const [mintLink, setMintLink] = useState("");
  const [collection, setCollection] = useState<Collection>();
  const [status, setStatus] = useState<"SUCCESS" | "ERROR" | "">("");

  useEffect(() => {
    if (collection) {
      setMintLink("https://omakasea.io/mint/" + collection.contractAddress);
    }
  }, [collection]);

  useEffect(() => {
    if (!props) {
      navigate("/", { replace: true });
    }
    if (props && props.collection) {
      getMintPageContent(props.collection.contractAddress).then(
        (collection) => {
          setCollection(collection);
        }
      );
      console.log(collection);

      setDiscord(collection?.discord || "");
      setInstagram(collection?.instagram || "");
      setTwitter(collection?.twitter || "");
      setOpensea(collection?.opensea || "");
    }
  }, [props, navigate]);

  const handleCopy = () => {
    setCopyButtonTooltip(uiText.copyButtonSuccessTooltip);
    setTimeout(() => {
      setCopyButtonTooltip(uiText.copyButtonTooltip);
    }, 5000);
  };

  const signMessage = async () => {
    const id = v4();
    const message = id;
    const result = await sign(account, message, "null");
    return {
      message,
      result,
      id,
    };
  };

  const uploadPageData = (res: any) => {
    let formData = new FormData();

    formData.append("validation", JSON.stringify(res));
    formData.append("contractAddress", collection?.contractAddress || "");
    formData.append(
      "socialLinks",
      JSON.stringify({ twitter, discord, instagram, opensea, telegram: "" })
    );
    formData.append("desktopImage", desktopImage);
    formData.append("mobileImage", mobileImage);

    const onSuccess = () => {
      setStatus("SUCCESS");
    };

    const onFailure = () => {
      setStatus("ERROR");
    };

    updateMintPage(formData, onSuccess, onFailure);
  };

  const handleUploadMintPage = async () => {
    await signMessage()
      .then(async ({ message, result, id }) => {
        uploadPageData({
          message,
          result,
          id,
        });
      })
      .catch((e) => {
        console.log(e);
        return;
      });
  };

  const handleViewMintPage = () => {
    navigate(`/mint/${address}`);
  };

  return (
    <Box flex={1} pt="74px" pb="136px" mx={isBigScreen ? "25em" : "191px"}>
      {status === "SUCCESS" && (
        <UpdatedNotificationModal
          clearStatus={() => setStatus("")}
          onViewMintPage={handleViewMintPage}
        />
      )}
      {status === "ERROR" && (
        <ErrorNotificationModal clearStatus={() => setStatus("")} />
      )}
      <Flex
        flexDir={"row"}
        justifyContent="space-between"
        alignItems={"center"}
      >
        <Text fontFamily={"heading"} fontWeight="bold" fontSize={"32px"}>
          {uiText.customizeMintTitle}
        </Text>
        <Flex flexDir={"column"} alignItems={"start"}>
          <Text
            fontFamily={"body"}
            fontSize="12px"
            mb="12px"
            color={DESCRIPTION_COLOR}
          >
            {uiText.customizeMintCollectionLink}
          </Text>
          <Flex>
            <Text
              fontFamily={"heading"}
              fontWeight="bold"
              fontSize={"18px"}
              mr="23px"
              w="320px"
              overflow={"hidden"}
              whiteSpace={"nowrap"}
              textOverflow={"ellipsis"}
            >
              {mintLink}
            </Text>
            <Tooltip
              label={copyButtonTooltip}
              closeOnClick={false}
              arrow
              shouldWrapChildren
            >
              <CopyToClipboard text={mintLink} onCopy={handleCopy}>
                <IconButton
                  mt="-7px"
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                  aria-label="button"
                  icon={<MdContentCopy />}
                />
              </CopyToClipboard>
            </Tooltip>
          </Flex>
        </Flex>
      </Flex>

      <Flex mt="77px" flexDir={"column"} alignItems="start">
        <Text fontFamily={"heading"} fontWeight="semibold" fontSize={"24px"}>
          {uiText.customizeMintBannerTitle}
        </Text>
        <Text
          fontFamily={"body"}
          fontSize={"16px"}
          color={APP_TEXT_INACTIVE_COLOR}
        >
          {uiText.customizeMintBannerDescription}
        </Text>
      </Flex>

      <Flex flexDir={"row"} justifyContent="space-between" mt="60px">
        <Flex flexDir={"column"}>
          <Flex alignItems={"center"} flexDir="row" mb="20px">
            <Text
              fontFamily={"heading"}
              fontSize="18px"
              fontWeight={"medium"}
              mr="8px"
            >
              {uiText.customizeMintDesktopTitle}
            </Text>
            <Text
              fontFamily={"body"}
              fontSize="11px"
              color={APP_TEXT_INACTIVE_COLOR}
            >
              {uiText.customizeMintDesktopDescription}
            </Text>
          </Flex>
          <Dropzone
            onDrop={(acceptedFiles) => {
              setDesktopImage(acceptedFiles[0]);
            }}
            multiple={false}
            accept="image/*"
            maxSize={10485760}
          >
            {({ getRootProps, getInputProps }) => (
              <Box
                w="650px"
                h="366px"
                backgroundColor="rgba(147,147,147,0.1)"
                position={"relative"}
                display="flex"
                justifyContent={"center"}
                alignItems="center"
                cursor={"pointer"}
                {...getRootProps()}
              >
                <input {...getInputProps()} />
                {!desktopImage && !collection?.mintPageDesktopImageUrl ? (
                  <>
                    <Flex flexDir={"column"} alignItems="center" mr="10px">
                      <ImageChakra
                        src="/add-image.png"
                        w="35px"
                        h="35px"
                        mb="14px"
                        opacity={0.5}
                      />
                      <Text
                        fontFamily={"body"}
                        fontWeight="semibold"
                        fontSize={"11px"}
                        color={UPLOAD_BOX_TEXT_COLOR}
                      >
                        {uiText.customizeMintBoxDescription}
                      </Text>
                    </Flex>
                    <Box
                      position={"absolute"}
                      bottom={"74px"}
                      left={0}
                      right={0}
                      margin={`0 auto`}
                      w="116px"
                      h="36px"
                      backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
                      backgroundImage={`url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='red' stroke-width='4' stroke-dasharray='6%2c 10'  stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e")`}
                      display="flex"
                      justifyContent="center"
                      alignItems={"center"}
                    >
                      <Text
                        fontFamily={"heading"}
                        fontWeight="semibold"
                        fontSize={"10px"}
                        color={APP_WARNING_COLOR}
                      >
                        {uiText.customizeMintButtonArea}
                      </Text>
                    </Box>
                  </>
                ) : (
                  <ImageChakra
                    w="650px"
                    h="366px"
                    src={
                      desktopImage
                        ? URL.createObjectURL(desktopImage)
                        : collection?.mintPageDesktopImageUrl
                    }
                    objectFit={"contain"}
                  />
                )}
              </Box>
            )}
          </Dropzone>
        </Flex>

        <Flex flexDir={"column"}>
          <Flex alignItems={"center"} flexDir="row" mb="20px">
            <Text
              fontFamily={"heading"}
              fontSize="18px"
              fontWeight={"medium"}
              mr="8px"
            >
              {uiText.customizeMintMobileTitle}
            </Text>
            <Text
              fontFamily={"body"}
              fontSize="11px"
              color={APP_TEXT_INACTIVE_COLOR}
            >
              {uiText.customizeMintMobileDescription}
            </Text>
          </Flex>
          <Dropzone
            onDrop={(acceptedFiles) => {
              setMobileImage(acceptedFiles[0]);
            }}
            multiple={false}
            accept="image/*"
            maxSize={10485760}
          >
            {({ getRootProps, getInputProps }) => (
              <Box
                w="240px"
                h="366px"
                backgroundColor="rgba(147,147,147,0.1)"
                position={"relative"}
                display="flex"
                justifyContent={"center"}
                alignItems="center"
                cursor={"pointer"}
                {...getRootProps()}
              >
                <input {...getInputProps()} />

                {!mobileImage && !collection?.mintPageMobileImageUrl ? (
                  <>
                    <Flex flexDir={"column"} alignItems="center">
                      <ImageChakra
                        src="/add-image.png"
                        w="35px"
                        h="35px"
                        mb="14px"
                        opacity={0.5}
                      />
                      <Text
                        fontFamily={"body"}
                        fontWeight="semibold"
                        fontSize={"11px"}
                        color={UPLOAD_BOX_TEXT_COLOR}
                      >
                        {uiText.customizeMintBoxDescription}
                      </Text>
                    </Flex>
                    <Box
                      position={"absolute"}
                      bottom={"74px"}
                      left={0}
                      right={0}
                      margin={`0 auto`}
                      w="116px"
                      h="36px"
                      backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
                      backgroundImage={`url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='red' stroke-width='4' stroke-dasharray='6%2c 10'  stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e")`}
                      display="flex"
                      justifyContent="center"
                      alignItems={"center"}
                    >
                      <Text
                        fontFamily={"heading"}
                        fontWeight="semibold"
                        fontSize={"10px"}
                        color={APP_WARNING_COLOR}
                      >
                        {uiText.customizeMintButtonArea}
                      </Text>
                    </Box>
                  </>
                ) : (
                  <ImageChakra
                    w="240px"
                    h="366px"
                    src={
                      mobileImage
                        ? URL.createObjectURL(mobileImage)
                        : collection?.mintPageMobileImageUrl
                    }
                    objectFit={"cover"}
                  />
                )}
              </Box>
            )}
          </Dropzone>
        </Flex>
      </Flex>

      <Flex mt="77px" flexDir={"column"} alignItems="start">
        <Text fontFamily={"heading"} fontWeight="semibold" fontSize={"24px"}>
          {uiText.customizeMintLinks}
        </Text>

        <Flex w="full" mt="48px">
          <Flex flex={1} flexDir={"column"} alignItems="start" mr={"64px"}>
            <Flex alignItems={"center"}>
              <ImageChakra
                src="/discord-black.png"
                w="14px"
                h="14px"
                mr="7px"
              />
              <Text
                fontFamily={"heading"}
                fontWeight="medium"
                fontSize={"15px"}
              >
                {uiText.discord}
              </Text>
            </Flex>
            <Input
              onChange={(e) => setDiscord(e.target.value)}
              defaultValue={discord}
              w="full"
              borderColor={APP_TEXT_INACTIVE_COLOR}
            />
          </Flex>
          <Flex flex={1} flexDir={"column"} alignItems="start">
            <Flex alignItems={"center"}>
              <ImageChakra
                src="/instagram-black.png"
                w="14px"
                h="14px"
                mr="7px"
              />
              <Text
                fontFamily={"heading"}
                fontWeight="medium"
                fontSize={"15px"}
              >
                {uiText.instagram}
              </Text>
            </Flex>
            <Input
              onChange={(e) => setInstagram(e.target.value)}
              defaultValue={instagram}
              w="full"
              borderColor={APP_TEXT_INACTIVE_COLOR}
            />
          </Flex>
        </Flex>

        <Flex w="full" mt={"22px"}>
          <Flex flex={1} flexDir={"column"} alignItems="start" mr={"64px"}>
            <Flex alignItems={"center"}>
              <ImageChakra
                src="/twitter-black.png"
                w="14px"
                h="14px"
                mr="7px"
              />
              <Text
                fontFamily={"heading"}
                fontWeight="medium"
                fontSize={"15px"}
              >
                {uiText.twitter}
              </Text>
            </Flex>
            <Input
              onChange={(e) => setTwitter(e.target.value)}
              w="full"
              borderColor={APP_TEXT_INACTIVE_COLOR}
              defaultValue={twitter}
            />
          </Flex>
          <Flex flex={1} flexDir={"column"} alignItems="start">
            <Flex alignItems={"center"}>
              <ImageChakra
                src="/opensea-black.png"
                w="14px"
                h="14px"
                mr="7px"
              />
              <Text
                fontFamily={"heading"}
                fontWeight="medium"
                fontSize={"15px"}
              >
                {uiText.opensea}
              </Text>
            </Flex>
            <Input
              onChange={(e) => setOpensea(e.target.value)}
              w="full"
              borderColor={APP_TEXT_INACTIVE_COLOR}
              defaultValue={opensea}
            />
          </Flex>
        </Flex>
      </Flex>
      <Flex justifyContent={"center"} mt={"110px"}>
        <PrimaryButton
          w="226px"
          h="54px"
          fontFamily={"heading"}
          fontWeight={"bold"}
          fontSize={"14px"}
          text={uiText.saveDetails}
          onClick={() => handleUploadMintPage()}
        />
        <PrimaryButton
          w="226px"
          h="54px"
          fontFamily={"heading"}
          fontWeight={"bold"}
          fontSize={"14px"}
          borderWidth={"1px"}
          ml={"30px"}
          _hover={{
            opacity: 0.8,
            backgroundColor: APP_IMAGE_PLACEHOLDER_COLOR,
          }}
          borderColor={APP_PRIMARY_COLOR}
          backgroundColor={APP_PRIMARY_CONTRAST_COLOR}
          text={uiText.viewMintPage}
          onClick={() => window.open(`/mint/${address}`, "_blank")?.focus()}
        />
      </Flex>
    </Box>
  );
}

export default CustomizeMint;
