import { Box, Button, chakra, Flex, Grid } from "@chakra-ui/react";
import { useContext, useEffect, useState } from "react";
import Axios from "axios";
import { v4 } from "uuid";
import { useLocation, useNavigate } from "react-router-dom";
import AttributeCard from "../../components/cards/AttributeCard";
import { useUiText } from "../../system/hooks/useUiText";
import {
  APP_PRIMARY_CONTRAST_COLOR,
  APP_SECONDARY_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";
import { CollectionFormDataType } from "../../types";
import { WalletContext } from "../../system/context/WalletContext";
import { utils, ContractFactory } from "ethers";
import ReviewCollectionContent from "../../components/labels/ReviewCollectionContent";
import { abi, bytecode } from "../../contracts/MasterchefMasatoshi.json";
import ReviewProcessingCard from "../../components/cards/ReviewProcessingCard";
import ReviewFailCard from "../../components/cards/ReviewFailCard";
import ReviewInsufficientCard from "../../components/cards/ReviewInsufficientCard";
import ReviewSuccessCard from "../../components/cards/ReviewSuccessCard";
import ReviewDeployingCard from "../../components/cards/ReviewDeployingCard";
import CollectionFailedCard from "../../components/cards/CollectionFailedCard";
const https = require("https");
const httpsAgent = new https.Agent({ rejectUnauthorized: false });

type ReviewStatusType =
  | "PROCESSING"
  | "SUCCESS"
  | "DEPLOYING"
  | "DEPLOYED"
  | "NOT_ENOUGH_TRAITS"
  | "INSUFFICIENT";

function ReviewCollection() {
  const [pageLoading, setPageLoading] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [status, setStatus] = useState<ReviewStatusType>("PROCESSING");
  const [isError, setIsError] = useState(false);
  const [imageWidth, setImageWidth] = useState(0);
  const [imageHeight, setImageHeight] = useState(0);
  let location = useLocation();
  const props: CollectionFormDataType = location.state;
  let navigate = useNavigate();
  const { uiText } = useUiText();
  if (!props) {
    navigate("/home", { replace: true });
  }
  const { account, ethereum, sign } = useContext(WalletContext);

  const [collectionId, setCollectionId] = useState("");

  useEffect(() => {
    if (props) {
      setPageLoading(false);
      const image = new Image();
      let url: string;
      image.onload = function () {
        setImageHeight(image.height);
        setImageWidth(image.width);
      };
      url = URL.createObjectURL(props.attributes[0].files[0]);
      image.src = url;
    }
  }, [props]);

  const signMessage = async () => {
    if (!ethereum) setIsError(true);
    const id = v4();
    const message = `0x${Buffer.from(id, "utf8").toString("hex")}`;
    const result = await sign(account, message, "null");
    return {
      message,
      result,
      id,
    };
  };

  const deployContract = async () => {
    if (!ethereum) setIsError(true);
    const signer = ethereum?.getSigner(account);
    const factory = new ContractFactory(abi, bytecode, signer);
    const { name, symbol, total, cost } = props;
    const contract = await factory.deploy(
      name,
      symbol,
      total,
      utils.parseEther(cost.toString()),
      "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2",
      "0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2"
    );
    await contract.deployTransaction.wait(3);
    const deployTxHash = contract.deployTransaction.hash;
    const contractAddress = contract.address;
    return {
      deployTxHash,
      contractAddress,
    };
  };

  const SubmitCollection = async (res: any) => {
    // Form Data
    let formData = new FormData();
    const id = res.id;
    setCollectionId(id);
    formData.append("collectionName", props.name);
    formData.append("symbol", props.symbol);
    formData.append("price", props.cost);
    formData.append("description", props.description);
    formData.append("creatorAddress", account!);
    formData.append("maxSupply", props.total);
    formData.append("id", id);
    formData.append("isPreview", "false");
    formData.append("validation", JSON.stringify(res));
    props.attributes.map((attribute) => {
      const filename = attribute.name;
      const files = attribute.files;
      return files.map((file) => {
        return formData.append(`${filename}`, file);
      });
    });
    const metadata: { [key: string]: string } = {};
    props.metadata.map((data) => {
      const key = data.key;
      const value = data.value;
      return (metadata[key] = value);
    });
    formData.append("imageHeight", String(imageHeight));
    formData.append("imageWidth", String(imageWidth));
    formData.append("extraMetadata", JSON.stringify(metadata));

    await Axios({
      method: "POST",
      httpsAgent,
      url: `https://${process.env.REACT_APP_SERVER_URL}.com/make`,
      data: formData,
      responseType: "blob",
      headers: { "Content-Type": "multipart/form-data" },
    })
      .then((res) => {
        setStatus("SUCCESS");
        return res;
      })
      .catch((err) => {
        setIsError(true);
        if (!err.response) {
          // network error
          console.log("Submit Collection", err.response, err);
          return Promise.reject("Error: Network Error");
        } else {
          console.log("Submit Collection", err.response);
          return Promise.reject(err);
        }
      });
  };

  const LaunchCollection = async () => {
    setIsLoading(true);
    setStatus("PROCESSING");
    signMessage()
      .then(async ({ message, result, id }) => {
        SubmitCollection({
          message,
          result,
          id,
        });
      })
      .catch((e) => {
        setIsError(true);
        return;
      });
  };

  const deployAndVerify = async () => {
    setStatus("DEPLOYING");
    const { deployTxHash, contractAddress } = await deployContract().catch(
      (err) => {
        setIsError(true);
        return Promise.reject(err);
      }
    );
    await Axios({
      method: "POST",
      responseType: "json",
      httpsAgent,
      url: `https://${process.env.REACT_APP_SERVER_URL}.com/verify`,
      data: {
        collectionId,
        deployTxHash,
        contractAddress,
        network: ethereum?.network?.name!,
      },
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => {
        setStatus("DEPLOYED");
        setIsLoading(false);
        navigate("/success-collection", {
          replace: true,
          state: { isFromReview: true, id: collectionId },
        });
        return res;
      })
      .catch((err) => {
        setIsError(true);
        if (!err.response) {
          // network error
          Promise.reject("Error: Network Error");
        } else {
          Promise.reject(err);
        }
      });
  };

  const renderCard = () => {
    if (isError) {
      return (
        <ReviewFailCard
          onClose={() => {
            setIsError(false);
            if (status !== "SUCCESS") {
              setIsLoading(false);
              setStatus("PROCESSING");
            }
          }}
        />
      );
    }
    switch (status) {
      case "PROCESSING":
        return <ReviewProcessingCard />;
      case "INSUFFICIENT":
        return (
          <ReviewInsufficientCard
            onClose={() => {
              setIsLoading(false);
              setStatus("PROCESSING");
            }}
          />
        );
      case "SUCCESS":
        return <ReviewSuccessCard onDeploy={deployAndVerify} />;
      case "DEPLOYING":
        return <ReviewDeployingCard />;
      case "NOT_ENOUGH_TRAITS":
        return (
          <CollectionFailedCard
            onClick={() =>
              navigate("/create-collection", { replace: true, state: props })
            }
            onClose={() => {
              setIsLoading(false);
              setStatus("PROCESSING");
            }}
          />
        );
    }
  };

  return pageLoading ? (
    <div>Loading</div>
  ) : (
    <Box px={"120px"} w="100%" mb={"180px"}>
      <Flex flexDir="row">
        {isLoading && renderCard()}
        {!isLoading && (
          <Flex flexDir={"column"} flex={1} mt={"140px"}>
            <chakra.h1 fontSize={"44px"} textAlign={"left"} mb={"26px"}>
              {uiText.reviewCollectionTitle}
            </chakra.h1>
            <chakra.h1
              color={APP_TEXT_INACTIVE_COLOR}
              fontSize={"15px"}
              textAlign={"left"}
              mb={"88px"}
            >
              {uiText.reviewCollectionTitleDescription}
            </chakra.h1>
            <Button
              onClick={() =>
                navigate("/create-collection", { replace: true, state: props })
              }
              fontSize={"16px"}
              width="100%"
              bgColor={APP_PRIMARY_CONTRAST_COLOR}
              borderWidth={"1px"}
              borderColor={{ HOMEPAGE_BACKGROUND_COLOR }}
              borderRadius={"8px"}
            >
              {uiText.reviewCollectionEditButtonLabel}
            </Button>

            <Button
              isDisabled={isLoading}
              isLoading={isLoading}
              onClick={LaunchCollection}
              fontSize={"16px"}
              bgColor={APP_SECONDARY_COLOR}
              borderRadius={"8px"}
              mt={"16px"}
            >
              {uiText.reviewCollectionCreateButton}
            </Button>
          </Flex>
        )}
        <Flex
          flexDir={"column"}
          alignItems={"flex-start"}
          flex={2}
          mt={"60px"}
          ml={"60px"}
        >
          <Flex
            mb={"72px"}
            flexDir={"row"}
            width="100%"
            justifyContent={"space-between"}
          >
            <ReviewCollectionContent
              label={uiText.reviewCollectionName}
              content={props.name}
              flex={1}
            />

            <ReviewCollectionContent
              label={uiText.reviewCollectionSymbol}
              content={props.symbol}
              flex={1}
            />
          </Flex>

          <ReviewCollectionContent
            label={uiText.reviewCollectionDescription}
            content={props.description}
          />
          <chakra.h1
            mt={"72px"}
            color={APP_TEXT_INACTIVE_COLOR}
            fontSize={"16px"}
            mb={"12px"}
          >
            {`${uiText.reviewCollectionUploaded1} (${props.attributes?.length} ${uiText.reviewCollectionUploaded2})`}
          </chakra.h1>
          <Grid marginTop="20px" templateColumns="repeat(4, 1fr)" gap={5}>
            {props.attributes?.map((attribute, index) => {
              const imageURL = URL.createObjectURL(attribute.files[0]);
              return (
                <AttributeCard
                  key={index}
                  name={attribute.name}
                  image={imageURL}
                  totalTraits={attribute.files.length}
                />
              );
            })}
          </Grid>
          <Flex
            mt={"72px"}
            flexDir={"row"}
            width="100%"
            justifyContent={"space-between"}
          >
            <ReviewCollectionContent
              label={uiText.reviewCollectionCost}
              content={props.cost}
              flex={1}
            />

            <ReviewCollectionContent
              label={uiText.reviewCollectionTotal}
              content={props.total}
              flex={1}
            />
          </Flex>
          <chakra.h1
            mt={"72px"}
            color={APP_TEXT_INACTIVE_COLOR}
            fontSize={"16px"}
          >
            {uiText.reviewCollectionMetadata}
          </chakra.h1>
          <Flex
            mt={"20px"}
            flexDir={"row"}
            width="100%"
            justifyContent={"space-between"}
          >
            <Box flex={1}>
              <chakra.h1
                textAlign={"left"}
                color={APP_TEXT_INACTIVE_COLOR}
                fontSize={"16px"}
              >
                {uiText.reviewCollectionKey}
              </chakra.h1>
            </Box>
            <Box flex={1}>
              <chakra.h1
                textAlign={"left"}
                color={APP_TEXT_INACTIVE_COLOR}
                fontSize={"16px"}
              >
                {uiText.reviewCollectionValue}
              </chakra.h1>
            </Box>
          </Flex>
          {props.metadata?.map((data, index) => {
            return (
              <Flex
                key={index}
                width="100%"
                flexDir={"row"}
                justifyContent={"space-between"}
              >
                <Box flex={1}>
                  <chakra.h1
                    textAlign={"left"}
                    color={HOMEPAGE_BACKGROUND_COLOR}
                    fontSize={"20px"}
                  >
                    {data.key || "-"}
                  </chakra.h1>
                </Box>
                <Box flex={1}>
                  <chakra.h1
                    textAlign={"left"}
                    color={HOMEPAGE_BACKGROUND_COLOR}
                    fontSize={"20px"}
                  >
                    {data.value || "-"}
                  </chakra.h1>
                </Box>
              </Flex>
            );
          })}
        </Flex>
      </Flex>
    </Box>
  );
}

export default ReviewCollection;
