import { Flex } from "@chakra-ui/react";
import { DescriptionPageLayoutProps } from "./types";

export const DescriptionPageLayout = ({
  introSection,
  sections,
  bgColor,
}: DescriptionPageLayoutProps) => {
  return (
    <Flex
      flexDir="column"
      alignItems="center"
      pr={[10, 10, 10, 0, 0]}
      pl={[10, 10, 10, 0, 0]}
      w="100%"
      bgColor={bgColor}
    >
      <Flex
        flexWrap="nowrap"
        flexDir={["column", "column", "column", "row", "row"]}
        justify="space-between"
        w="100%"
        mb={[100, 100, 200, 200]}
      >
        {/* Intro Section */}
        {introSection}
      </Flex>
      {/* Additional Sections */}
      {sections.map((section, i) => {
        if (i % 2 === 0) {
          return (
            <Flex
              key={`key-${i}`}
              flexDir={[
                "column-reverse",
                "column-reverse",
                "column-reverse",
                "row",
                "row",
              ]}
              mb={100}
            >
              {section}
            </Flex>
          );
        } else {
          return (
            <Flex
              key={`key-${i}`}
              flexDir={["column", "column", "column", "row", "row"]}
              mb={150}
              w={["100%", "100%", "100%", "auto", "auto"]}
            >
              {section}
            </Flex>
          );
        }
      })}
    </Flex>
  );
};
