import { Flex } from "@chakra-ui/layout";
import { Text, Image, Button } from "@chakra-ui/react";
import { useContext } from "react";
import { SecondaryButton } from "../../../components/buttons/SecondaryButton";
import { CollectorContext } from "../../../system/context/CollectorContext";
import useDeviceDetect from "../../../system/hooks/useDeviceDetect";
import { useUiText } from "../../../system/hooks/useUiText";
import {
  APP_DIVIDER_COLOR,
  APP_PRIMARY_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
} from "../../../theme/colors";
import { WalletModalConnectViewProps } from "./types";

export const WalletModalConnectView = ({
  handleConnect,
  onClose,
}: WalletModalConnectViewProps) => {
  const { uiText } = useUiText();
  const { isMobile } = useDeviceDetect();
  const { isMintPage } = useContext(CollectorContext);
  const isMobileMint = isMobile && isMintPage;
  const wallets = [
    { src: "/metamask-fox.svg", text: uiText.metamask },
    { src: "/safepal.png", text: uiText.safepal },
    { src: "/trustWallet.png", text: uiText.trustWallet },
    { src: "/tokenPocket.png", text: uiText.tokenPocket },
    { src: "/walletConnect.png", text: uiText.walletConnect },
  ];

  const renderWalletSelection = (src: string, text: string, last: boolean) => {
    const connect = () => {
      handleConnect(text === "Metamask" ? true : false);
    };

    return (
      <Flex
        key={`wallet-${text}`}
        w="100%"
        flexDirection="row"
        alignItems="center"
        _hover={{ cursor: "pointer", backgroundColor: "#f2f2f2" }}
        onClick={connect}
        borderBottom={last ? "none" : "1px solid"}
        borderColor={APP_DIVIDER_COLOR}
        py={"20px"}
        pl={1}
      >
        <Image src={src} w="37px" h="37px" mr="34px" objectFit={"contain"} />
        <Text
          fontFamily="heading"
          fontWeight="medium"
          fontSize={"16px"}
          color={APP_PRIMARY_COLOR}
        >
          {text}
        </Text>
      </Flex>
    );
  };

  return (
    <Flex
      position={"relative"}
      flexDir="column"
      height="100%"
      paddingY="48px"
      paddingX="43px"
    >
      {isMobileMint && (
        <Button
          onClick={onClose}
          _hover={{
            borderColor: "transparent",
            bgColor: APP_PRIMARY_CONTRAST_COLOR,
          }}
          _active={{
            borderColor: "transparent",
            bgColor: APP_PRIMARY_CONTRAST_COLOR,
          }}
          _focus={{
            borderColor: "transparent",
            bgColor: APP_PRIMARY_CONTRAST_COLOR,
          }}
          top={0}
          w={"full"}
          position={"absolute"}
          alignSelf="center"
          bgColor={APP_PRIMARY_CONTRAST_COLOR}
        >
          <Image
            src={"/arrow-down.png"}
            w="40px"
            h="30px"
            objectFit={"contain"}
          />
        </Button>
      )}
      <Text
        fontSize={"28px"}
        fontWeight="bold"
        fontFamily={"heading"}
        mb="23px"
      >
        {uiText.selectWallet}
      </Text>
      <Flex flexDir="column" h="100%" align="center">
        {wallets.map((w, i) =>
          renderWalletSelection(w.src, w.text, i === wallets.length - 1)
        )}
      </Flex>
      <SecondaryButton
        borderColor={APP_PRIMARY_COLOR}
        alignSelf="center"
        width={"205px"}
        fontSize="12px"
        fontFamily={"heading"}
        py='15px'
        mt={"20px"}
        text={uiText.cancel}
        onClick={() => onClose()}
      />
    </Flex>
  );
};
