import {
  Flex,
  Image,
  Box,
  Menu,
  MenuButton,
  MenuItem,
  MenuList,
  IconButton,
} from "@chakra-ui/react";
import {
  APP_HEADER_BACKGROUND_COLOR,
  HOMEPAGE_BACKGROUND_COLOR,
} from "../../theme/colors";
import React, { useContext } from "react";
import { Link, useLocation } from "react-router-dom";
import { WalletContext } from "../../system/context/WalletContext";
import { ConnectWalletButton } from "../../components/buttons";
import { ModalContext } from "../../system/context/ModalContext";
import { HeaderButton } from "../../components/buttons/HeaderButton";
import { useUiText } from "../../system/hooks/useUiText";
import { useScreenSize } from "../../system/hooks/useScreenSize";
import { GiHamburgerMenu } from "react-icons/gi";
import { ContractContext } from "../../system/context/ContractContext";
import { FaDiscord } from "react-icons/fa";
import { BsChevronDown } from "react-icons/bs";
import { DISCORD_PAGE_HREF } from "../../system/constants";
import { useNoReceipt } from "../../pages/Modals/NoReceiptModal/useNoReceipt";
import { CollectorContext } from "../../system/context/CollectorContext";

export const AppHeader = () => {
  const { accountChangePending, account } = React.useContext(WalletContext);
  const { hasReceipt } = useContext(ContractContext);
  const { walletModal } = useContext(ModalContext);
  const { uiText } = useUiText();
  const { isWidthOrGreater } = useScreenSize(1024);
  const { handleCreateNewCollection } = useNoReceipt();
  const location = useLocation();
  const { isMintPage } = useContext(CollectorContext);

  const handleConnect = () => {
    walletModal.onOpen();
  };

  const navItems = [
    <HeaderButton
      text={uiText.startNewCollection}
      disabled={location.pathname === "/create-collection" || !hasReceipt}
      display={!account ? "none" : "block"}
      onClick={handleCreateNewCollection}
      mr={"30px"}
    />,
    <Link to="/kitchen">
      <HeaderButton
        text={uiText.enterKitchen}
        disabled={location.pathname === "/kitchen"}
        display={!account ? "none" : "block"}
        mr={"30px"}
      />
    </Link>,
    <HeaderButton
      text={uiText.mintReceipt}
      disabled={!account}
      onClick={handleConnect}
      display={!account ? "none" : "block"}
    />,
    <Flex flexDir="row" alignItems="center">
      {account ? (
        !isMintPage ? (
          <HeaderButton
            text={uiText.myProfile}
            onClick={handleConnect}
            rightIcon={<BsChevronDown color="#95ffce" />}
            profile
          />
        ) : (
          <ConnectWalletButton
            isConnected={!!account}
            onClick={handleConnect}
            connectPending={accountChangePending}
          />
        )
      ) : (
        <ConnectWalletButton
          isConnected={!!account}
          onClick={handleConnect}
          connectPending={accountChangePending}
        />
      )}
    </Flex>,
    <IconButton
      aria-label="link"
      as="a"
      href={DISCORD_PAGE_HREF}
      bg="transparent"
      icon={<FaDiscord size="22px" color="#fff" />}
      _hover={{ bgColor: "#282828" }}
      ml="35px"
    />,
  ];

  const mobileHeader = () => {
    return !isMintPage ? (
      <Menu>
        <MenuButton
          as={IconButton}
          icon={<GiHamburgerMenu />}
          colorScheme={"black"}
        />
        <MenuList bgColor={HOMEPAGE_BACKGROUND_COLOR}>
          {navItems.map((n, i) => (
            <MenuItem
              key={`nav-item-${i}`}
              _hover={{ bg: "transparent" }}
              _active={{ bgColor: "transparent" }}
              _focus={{ bgColor: "transparent" }}
            >
              {n}
            </MenuItem>
          ))}
        </MenuList>
      </Menu>
    ) : (
      <Flex justify={"center"}>{navItems[3]}</Flex>
    );
  };

  return (
    <Flex
      backgroundColor={APP_HEADER_BACKGROUND_COLOR}
      minH="40px"
      paddingLeft={['25px', '25px', "70px"]}
      paddingRight="32px"
      py={"20px"}
      position="sticky"
      top={0}
      zIndex={10}
      justifyContent="space-between"
    >
      <Flex alignItems="center">
        {isWidthOrGreater ? (
          <Box w={120}>
            <Link to="/home">
              <Image objectFit="contain" h={8} src="/logo_omakasea.png" />
            </Link>
          </Box>
        ) : (
          <Box w={200}>
            <Link to="/home">
              <Image h={8} src="/logo_omakasea.png" marginRight={2} />
            </Link>
          </Box>
        )}
      </Flex>
      <Flex>
        {isWidthOrGreater ? (
          mobileHeader()
        ) : (
          <>
            {isMintPage ? (
              <Flex justify={"center"}>{navItems[3]}</Flex>
            ) : (
              navItems.map((n, i) => {
                return (
                  <Flex key={`nav-item-${i}`} justify={"center"}>
                    {n}
                  </Flex>
                );
              })
            )}
          </>
        )}
      </Flex>
    </Flex>
  );
};
