import axios from "axios";
import { mapApiCollectionToCollection, mintPageContentMapper } from "./mappers";
import { ApiCollection, Collection } from "./types";
import { utils } from "ethers";

const baseURL = `https://${process.env.REACT_APP_SERVER_URL}.com`;

const apiGet = axios.create({
  baseURL,
  headers: { "Content-Type": "application/json" },
});

const apiCreate = axios.create({
  baseURL,
  headers: { "Content-Type": "multipart/form-data" },
  responseType: "blob",
});

export const getCollections = async (
  creatorAddress: string,
  validation: string
): Promise<Collection[]> => {
  creatorAddress = utils.getAddress(creatorAddress);
  apiGet.defaults.responseType = "json";

  const data: { collections: ApiCollection[] } = await apiGet
    .post("/getCollectionsByCreatorAddress", { creatorAddress, validation })
    .then((res) => {
      return res.data;
    })
    .catch((err) => {
      console.log(err);
      if (err.response) {
        console.log("getCollectionsAPI", err.response);
      }
    });

  const collections = data?.collections?.map((c) =>
    mapApiCollectionToCollection(c)
  );
  return collections;
};

export const getSingleCollection = async (
  contractAddress: string,
  validation: string
): Promise<any> => {
  const data = await apiGet
    .post("/getCollectionByContractAddress", { contractAddress, validation })
    .then((res) => {
      return res.data;
    })
    .catch((err) => {
      console.log(err);
      if (err.response) {
        console.log("getCollectionByContractAddress", err.response);
      }
    });

  const collection = mapApiCollectionToCollection(data.collections);
  return collection;
};

export const getMintPageContent = async (
  contractAddress: string
): Promise<any> => {
  const data = await apiGet
    .post("/getMintPageContent", { contractAddress })
    .then((res) => {
      return res.data;
    })
    .catch((err) => {
      console.log(err);
      if (err.response) {
        console.log("getMintPageContent", err.response);
      }
    });

  const mintPageContent = mintPageContentMapper(data);
  return mintPageContent;
};

export const getNftImagesByIds = async (
  contractAddress: string,
  nftIds: string[],
  validation: string
): Promise<any> => {
  const data = await apiGet
    .post("/getNftImagesByIds", { nftIds, validation, contractAddress })
    .then((res) => {
      return res.data;
    })
    .catch((err) => {
      console.log(err);
      if (err.response) {
        console.log("getNftImagesByIds", err.response);
      }
    });

  return data;
};

export const updateMintPage = async (
  data: any,
  success: () => void,
  failure: () => void
) => {
  const mintPage = await apiCreate
    .post("/uploadMintPage", data)
    .then((res) => {
      success();
      return res.data;
    })
    .catch((err) => {
      console.log(err);
      if (err.response) {
        console.log("uploadMintPageApi", err.response);
        failure();
      }
    });

  return mintPage;
};

