import "./App.css";
import { AppContainer } from "./layout/AppContainer/AppContainer";
import { HomePage } from "./pages/HomePage/HomePage";
import { Routes, Route, Navigate } from "react-router-dom";
import { LandingPage } from "./pages/LandingPage/LandingPage";
import ReviewCollection from "./pages/ReviewCollection/ReviewCollection";
import CreateCollection from "./pages/CreateCollection/CreateCollection";
import { KitchenPage } from "./pages/KitchenPage/KitchenPage";
import SuccessCollection from "./pages/SuccessCollection/SuccessCollection";
import MobileRestriction from "./layout/MobileRestriction/MobileRestriction";
import { ReceiptRestriction } from "./layout/ReceiptRestriction/ReceiptRestriction";
import ScrollToTop from "./layout/ScrollToTop/ScrollToTop";
import Mint from "./pages/Mint/Mint";
import CustomizeMint from "./pages/CustomizeMint/CustomizeMint";

const App = () => {
  return (
    <div className="App">
      <ScrollToTop>
        <Routes>
          <Route path="/" element={<LandingPage />} />
          <Route
            path="/home"
            element={
              <MobileRestriction>
                <AppContainer>
                  <HomePage />
                </AppContainer>
              </MobileRestriction>
            }
          />
          <Route
            path="/kitchen"
            element={
              <MobileRestriction>
                <AppContainer>
                  <KitchenPage />
                </AppContainer>
              </MobileRestriction>
            }
          />
          <Route
            path="/create-collection"
            element={
              <MobileRestriction>
                <ReceiptRestriction>
                  <AppContainer>
                    <CreateCollection />
                  </AppContainer>
                </ReceiptRestriction>
              </MobileRestriction>
            }
          />
          <Route
            path="/review-collection"
            element={
              <MobileRestriction>
                <AppContainer>
                  <ReviewCollection />
                </AppContainer>
              </MobileRestriction>
            }
          />
          <Route
            path="/success-collection"
            element={
              <MobileRestriction>
                <AppContainer>
                  <SuccessCollection />
                </AppContainer>
              </MobileRestriction>
            }
          />
          <Route
            path="/mint/:address"
            element={
              <AppContainer>
                <Mint />
              </AppContainer>
            }
          >
            <Route
              path="/:address"
              element={
                <AppContainer>
                  <Mint />
                </AppContainer>
              }
            />
          </Route>
          <Route
            path="/customize"
            element={
              <MobileRestriction>
                <AppContainer>
                  <CustomizeMint />
                </AppContainer>
              </MobileRestriction>
            }
          >
            <Route
              path="/:address"
              element={
                <MobileRestriction>
                  <AppContainer>
                    <CustomizeMint />
                  </AppContainer>
                </MobileRestriction>
              }
            />
          </Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </ScrollToTop>
    </div>
  );
};

export default App;
