import {
  Heading,
  Button,
  Box,
  ModalHeader,
  Text,
  Flex,
  IconButton,
  Divider,
  Tooltip,
  Image,
} from "@chakra-ui/react";
import {
  APP_IMAGE_PLACEHOLDER_COLOR,
  APP_PRIMARY_COLOR,
  APP_PRIMARY_CONTRAST_COLOR,
  APP_TEXT_INACTIVE_COLOR,
  COLLECTION_IMAGE_BACKGROUND,
  COLLECTION_IMAGE_TEXT,
} from "../../../theme/colors";
import { CollectionDetailsProps } from "./types";
import { MdContentCopy } from "react-icons/md";
import { AiOutlineArrowLeft } from "react-icons/ai";
import { PrimaryButton } from "../../../components/buttons";
import { useUiText } from "../../../system/hooks/useUiText";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { AiFillQuestionCircle } from "react-icons/ai";
import React, { useState } from "react";
import { EthereumRequests } from "../../../system/hooks/ethereum/ethereumRequests";
import { Web3Provider } from "@ethersproject/providers";
import { WalletContext } from "../../../system/context/WalletContext";
import { WarningButton } from "../../../components/buttons/WarningButton";
import { ModalContext } from "../../../system/context/ModalContext";
import { CollectionContractContext } from "./CollectionContractContext";
import { useNavigate } from "react-router-dom";
import Dropzone, { FileWithPath } from "react-dropzone";
import { uploadPlaceholderMetaData } from "../../../system/api/api";

const INFO_ICON_BG_COLOR = "#b2b2b2";

export const CollectionDetails = ({
  collection,
  onBackToCollections,
  goToEnableMinting,
  goToProvenance,
  goToRevealCollection,
}: CollectionDetailsProps) => {
  const { uiText } = useUiText();
  const navigate = useNavigate();
  const { ethereum, validation } = React.useContext(WalletContext);
  const { walletModal } = React.useContext(ModalContext);

  const {
    saleIsActive,
    enableMinting,
    transactionPending,
    getTotalSupply,
    totalSupply,
    baseUri,
    withDrawEth,
    provenance,
  } = React.useContext(CollectionContractContext);
  const { transactionPendingModal } = React.useContext(ModalContext);
  const [collectionThumbnail, setCollectionThumbnail] = useState("");
  const [collectionThumbnailFile, setCollectionThumbnailFile] =
    useState<FileWithPath>();
  const [copyButtonTooltip, setCopyButtonTooltip] = React.useState<string>(
    uiText.copyButtonTooltip
  );
  const [balance, setBalance] = React.useState(0);

  const handleGoToEnableMinting = () => {
    goToEnableMinting(true);
  };

  const handleGoToProvenance = () => {
    goToProvenance(true);
  };

  const handleDisableMinting = () => {
    enableMinting();
  };

  const handleCopy = () => {
    setCopyButtonTooltip(uiText.copyButtonSuccessTooltip);
    setTimeout(() => {
      setCopyButtonTooltip(uiText.copyButtonTooltip);
    }, 5000);
  };

  const handleBalance = async (ethereum: Web3Provider, address: string) => {
    const requests = new EthereumRequests(ethereum);
    const balance = await requests.getBalance(address).then((balance) => {
      const value = parseInt(balance) / Math.pow(10, 18) || 0;
      return value;
    });
    return balance;
  };

  const handleWithdrawEth = () => {
    if (balance === 0)
      if (ethereum) {
        withDrawEth(ethereum, collection.contractAddress);
      }
  };

  const uploadImageData = () => {
    let formData = new FormData();

    formData.append("validation", validation);
    formData.append("contractAddress", collection?.contractAddress || "");
    formData.append("description", "");
    formData.append("placeholders", collectionThumbnailFile as FileWithPath);

    uploadPlaceholderMetaData(formData);
  };

  React.useEffect(() => {
    if (ethereum) {
      handleBalance(ethereum, collection.contractAddress).then((b) =>
        setBalance(b)
      );
    }
  }, [collection, ethereum]);

  React.useEffect(() => {
    if (transactionPending) {
      transactionPendingModal.onOpen();
    }
  }, [transactionPending, transactionPendingModal]);

  React.useEffect(() => {
    getTotalSupply();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useEffect(() => {
    if (collectionThumbnailFile) {
      uploadImageData();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [collectionThumbnailFile]);

  const renderEnableMintingButtons = () => {
    return saleIsActive ? (
      <WarningButton
        w="160px"
        text={uiText.pauseMinting}
        onClick={handleDisableMinting}
      />
    ) : (
      <PrimaryButton
        w="160px"
        fontFamily={"heading"}
        fontWeight={"bold"}
        fontSize={"12px"}
        text={provenance ? uiText.enableMinting : uiText.startSale}
        onClick={provenance ? handleGoToEnableMinting : handleGoToProvenance}
      />
    );
  };

  const openInNewTab = (url: string) => {
    window.open(url, "_blank")?.focus();
  };

  const renderCollectionImage = () => {
    return !collection.collectionImage ? (
      <Image
        h={"236px"}
        w={"210px"}
        objectFit="cover"
        borderRadius="5px"
        src={collection.collectionImage}
        bgColor={APP_IMAGE_PLACEHOLDER_COLOR}
        mr={"32px"}
      />
    ) : (
      <Dropzone
        onDrop={(acceptedFiles) => {
          //auto save from here
          setCollectionThumbnail(URL.createObjectURL(acceptedFiles[0]));
          setCollectionThumbnailFile(acceptedFiles[0]);
        }}
        multiple={false}
        accept="image/*"
        maxSize={10485760}
      >
        {({ getRootProps, getInputProps }) => (
          <Flex
            h={"236px"}
            w={"224px"}
            bgColor={COLLECTION_IMAGE_BACKGROUND}
            mr={"32px"}
            borderRadius="5px"
            flexDir={"column"}
            justifyContent={"center"}
            alignItems={"center"}
            {...getRootProps()}
          >
            <input {...getInputProps()} />
            {collectionThumbnail ? (
              <Image
                h={"236px"}
                w={"224px"}
                objectFit="cover"
                borderRadius="5px"
                src={collectionThumbnail}
                bgColor={APP_IMAGE_PLACEHOLDER_COLOR}
              />
            ) : (
              <Flex
                flexDir={"column"}
                justifyContent={"center"}
                alignItems={"center"}
              >
                <Image
                  src="/add-image.png"
                  w="35px"
                  h="35px"
                  objectFit={"contain"}
                  opacity={0.25}
                  mb="17px"
                />
                <Text
                  fontFamily={"heading"}
                  fontSize="8px"
                  fontWeight={"medium"}
                  color={COLLECTION_IMAGE_TEXT}
                  opacity={0.25}
                  w="52px"
                  textAlign={"center"}
                >
                  {uiText.addPreviewImage}
                </Text>
              </Flex>
            )}
          </Flex>
        )}
      </Dropzone>
    );
  };

  return (
    <Box
      overflow={"auto"}
      display={"flex"}
      flexDir={"column"}
      justifyContent={"space-between"}
      pb={10}
    >
      <Box>
        <ModalHeader position="relative">
          <Text
            fontFamily={"body"}
            fontWeight={"normal"}
            color={APP_TEXT_INACTIVE_COLOR}
            fontSize="12px"
          >
            {collection?.symbol}
          </Text>
          <Heading fontSize={"28px"} fontWeight={"bold"}>
            {collection?.name}
          </Heading>
          <IconButton
            aria-label="button"
            position="absolute"
            top={2}
            right={5}
            onClick={onBackToCollections}
            size={"lg"}
            bg="transparent"
            _focus={{ border: "none" }}
            _hover={{ cursor: "pointer" }}
            icon={<AiOutlineArrowLeft />}
          />
        </ModalHeader>
        <Flex flexDir="column" p="5px 30px 30px 30px">
          <Flex mb={5}>
            {renderCollectionImage()}
            <Flex flexDir="column" justifyContent="center">
              <Box>
                <Text
                  fontFamily={"heading"}
                  fontSize="11px"
                  fontWeight={"bold"}
                  w="80px"
                >
                  {uiText.createdOn}
                </Text>
                <Text fontSize="9px" fontFamily={"aeonik"}>
                  {collection.dateCreated}
                </Text>
              </Box>
              <Box mt={"32px"} w="80px">
                <Text
                  fontFamily={"heading"}
                  fontWeight={"bold"}
                  fontSize="11px"
                >
                  {uiText.totalMinted}
                </Text>
                <Text fontSize="9px" fontFamily={"aeonik"}>
                  {totalSupply}
                </Text>
              </Box>
              <Flex
                flex={1}
                flexDir={"column"}
                alignItems={"center"}
                justifyContent={"flex-end"}
              >
                <Button
                  onClick={() =>
                    openInNewTab(
                      `https://etherscan.io/address/${collection.contractAddress}`
                    )
                  }
                  height={"39px"}
                  width={"30px"}
                  borderRadius={"40px"}
                  display={"flex"}
                  alignItems={"center"}
                  justifyContent={"center"}
                  p="0"
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                >
                  <Image
                    alt="Etherscan"
                    w="30px"
                    h="29px"
                    objectFit={"contain"}
                    src="./etherscan.png"
                  />
                </Button>
                <Button
                  alt="Opensea"
                  disabled
                  mx={"5px"}
                  height={"39px"}
                  width={"30px"}
                  borderRadius={"40px"}
                  display={"flex"}
                  alignItems={"center"}
                  justifyContent={"center"}
                  p="0"
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                >
                  <Image
                    w="30px"
                    h="29px"
                    objectFit={"contain"}
                    src="./opensea.png"
                  />
                </Button>
                <Button
                  onClick={() => {
                    const metaDataBaseUrl = collection.metaDataBaseUrl;
                    const ipfs = metaDataBaseUrl.split("ipfs://")[1];
                    openInNewTab(`https://ipfs.io/ipfs/${ipfs}`);
                  }}
                  alt="IPFS"
                  height={"39px"}
                  width={"30px"}
                  borderRadius={"40px"}
                  display={"flex"}
                  alignItems={"center"}
                  justifyContent={"center"}
                  p="0"
                  bgColor={APP_PRIMARY_CONTRAST_COLOR}
                >
                  <Image
                    w="30px"
                    h="29px"
                    objectFit={"contain"}
                    src="./ipfs.png"
                  />
                </Button>
              </Flex>
            </Flex>
          </Flex>
          <Flex flexDir="column">
            <Flex justify="space-between" mb={3}>
              <Box>
                <Text
                  fontFamily={"heading"}
                  fontWeight={"bold"}
                  fontSize="11px"
                >
                  {uiText.contractAddress}
                </Text>
                <Text
                  mt="4px"
                  w="238px"
                  fontFamily={"body"}
                  whiteSpace={"nowrap"}
                  overflow={"hidden"}
                  textOverflow={"ellipsis"}
                  color={APP_TEXT_INACTIVE_COLOR}
                  fontSize={"10px"}
                >
                  {collection.contractAddress}
                </Text>
              </Box>
              <Tooltip
                label={copyButtonTooltip}
                closeOnClick={false}
                arrow
                shouldWrapChildren
              >
                <CopyToClipboard
                  text={collection.contractAddress}
                  onCopy={handleCopy}
                >
                  <IconButton
                    bgColor={APP_PRIMARY_CONTRAST_COLOR}
                    aria-label="button"
                    icon={<MdContentCopy />}
                  />
                </CopyToClipboard>
              </Tooltip>
            </Flex>
            <Divider mb={3} />
            <Flex
              flexDir={"row"}
              alignItems={"center"}
              justify="space-between"
              mb={3}
            >
              <Box>
                <Flex alignItems={"center"}>
                  <Text
                    fontFamily={"heading"}
                    fontWeight={"bold"}
                    fontSize="11px"
                    mr={"10px"}
                  >
                    {uiText.contractBalance}
                  </Text>
                  <Tooltip
                    label={uiText.withdrawFundsTooltip}
                    placement="top"
                    fontFamily={"body"}
                    fontWeight={"bold"}
                    fontSize="11px"
                    borderRadius={"5px"}
                    p={"12px"}
                    maxW={200}
                    hasArrow
                    shouldWrapChildren
                  >
                    <AiFillQuestionCircle
                      color={INFO_ICON_BG_COLOR}
                      cursor="pointer"
                      size={"11px"}
                    />
                  </Tooltip>
                </Flex>
                <Flex flexDir={"row"} fontFamily={"row"} mt={"4px"}>
                  <Flex flex={"col"}>
                    <Text
                      fontSize={"10px"}
                      fontFamily={"body"}
                      fontWeight={"normal"}
                      color={APP_TEXT_INACTIVE_COLOR}
                      mr={1}
                    >
                      {balance}
                    </Text>
                    <Text
                      fontFamily={"body"}
                      fontSize="10px"
                      color={APP_TEXT_INACTIVE_COLOR}
                    >
                      {uiText.ethereumAbbr}
                    </Text>
                  </Flex>
                </Flex>
              </Box>
              <Button
                color="black"
                borderColor={APP_PRIMARY_COLOR}
                variant="outline"
                borderRadius="20px"
                disabled={balance === 0}
                onClick={handleWithdrawEth}
                fontSize="8px"
                w="87px"
                h={"23px"}
                fontFamily={"body"}
                fontWeight={"bold"}
              >
                {uiText.withdrawETHButtonLabel}
              </Button>
            </Flex>
            <Divider mb={3} />
            <Flex justify="space-between">
              <Box>
                <Flex alignItems={"center"}>
                  <Text
                    fontFamily={"heading"}
                    fontWeight={"bold"}
                    fontSize="11px"
                    mr={"10px"}
                  >
                    {baseUri
                      ? uiText.collectionRevealed
                      : uiText.revealCollection}
                  </Text>
                  <Tooltip
                    label={uiText.revealCollectionMessage}
                    placement="top"
                    hasArrow
                    shouldWrapChildren
                    fontFamily={"body"}
                    fontWeight={"bold"}
                    fontSize="11px"
                    borderRadius={"5px"}
                    p={"12px"}
                  >
                    <AiFillQuestionCircle
                      color={INFO_ICON_BG_COLOR}
                      cursor="pointer"
                      size={"11px"}
                    />
                  </Tooltip>
                </Flex>
              </Box>
              {!baseUri && (
                <Button
                  color="black"
                  borderColor={APP_PRIMARY_COLOR}
                  variant="outline"
                  borderRadius="20px"
                  fontSize="8px"
                  fontFamily={"body"}
                  fontWeight={"bold"}
                  w="87px"
                  h={"23px"}
                  onClick={() => goToRevealCollection(true)}
                >
                  {uiText.revealCollection}
                </Button>
              )}
            </Flex>
          </Flex>
        </Flex>
      </Box>
      <Flex mx={"30px"} flexDir={"row"} justifyContent={"space-between"}>
        <PrimaryButton
          w="160px"
          fontFamily={"heading"}
          fontWeight={"bold"}
          fontSize={"12px"}
          backgroundColor={APP_PRIMARY_COLOR}
          _hover={{
            opacity: 0.8,
          }}
          color={APP_PRIMARY_CONTRAST_COLOR}
          text={uiText.mintPage}
          mr="10px"
          onClick={() => {
            walletModal.onClose();
            navigate(`/customize/${collection.contractAddress}`, {
              state: { collection },
            });
          }}
        />
        <Flex justify="center">{renderEnableMintingButtons()}</Flex>
      </Flex>
    </Box>
  );
};
