import { Box, Flex } from "@chakra-ui/react";
import { ModalPageNumberProps } from "./types";

export const ModalPageNumber = ({
  numberOfPages,
  currentPage,
}: ModalPageNumberProps) => {
  const renderPageNumbers = () => {
    const pageNumber = [];
    for (var i = 1; i <= numberOfPages; i++) {
      pageNumber.push(
        <Box
          color={currentPage === i ? "white" : "black"}
          border="0.5px solid black"
          borderRadius="40px"
          p="4px"
          pl="8px"
          pr="8px"
          mr="4px"
          fontSize="8px"
          bgColor={currentPage === i ? "black" : "white"}
        >
          {i}
        </Box>
      );
    }
    return pageNumber;
  };
  return <Flex>{renderPageNumbers()}</Flex>;
};
